import React from "react";
import { useEffect } from "react";
import { useLocation, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { ExerciseTable } from "../../components/exercise/ExerciseTable";
import { fetchExercises } from "../../features/exercise/exercise-slice";
import { BreadCrumbHome } from "../../components/breadcrumbs/BreadCrumbHome";
import { BreadCrumbLink } from "../../components/breadcrumbs/BreadCrumbLink";
import { BreadCrumbCurrentPage } from "../../components/breadcrumbs/BreadCrumbCurrentPage";
import { BreadCrumb } from "../../components/breadcrumbs/BreadCrumb";
import { ExerciseUpload } from "../../components/exercise/ExerciseUpload";
import { VideoForm } from "../../components/videos/VideoForm";
import { fetchVideos, createVideo } from "../../features/video/video-slice";
import { VideoTable } from "../../components/videos/VideoTable";

export const Exercise = () => {
  const dispatch = useDispatch();
  const { categoryId, chapterId, clas } = useParams();
  const {
    state,
  } = useLocation();
  const { name, categoryName, type, subject, lang } = state || {}
  const { exercises } = useSelector((state) => state.exercise);
  const { videos } = useSelector((state) => state.video);

  const onSubmit = (video) => {
    dispatch(createVideo({ categoryId, chapterId, ...video }));
  };

  useEffect(() => {
    if (type === "pdf") {
      dispatch(fetchExercises({ categoryId, chapterId }));
    } else dispatch(fetchVideos({ categoryId, chapterId }));
  }, [categoryId, chapterId, type, dispatch]);

  return (
    <div className="flex gap-2">
      <div className="flex flex-col gap-2 w-[calc(100%-19rem)] overflow-auto p-2">
        <BreadCrumb>
          <BreadCrumbHome title={clas} />
          <BreadCrumbLink title={subject?.name} path={"/content/" + clas + "?lang=" + lang + "&subjectId=" + subject?._id} />
          <BreadCrumbLink
            title={categoryName}
            path={"/content/" + clas + "/" + categoryId + "?lang=" + lang + "&subjectId=" + subject?._id}
            state={{ subject, clas, hasExercise: true, type, name: categoryName, lang }}
          />
          <BreadCrumbCurrentPage title={name} />
        </BreadCrumb>
        <div className="max-h-[90vh] overflow-auto">
          {type === "pdf" ? (
            exercises.length === 0 ? (
              <div className="flex-1">
                <p className="text-red-400">No exercise found</p>
              </div>
            ) : (
              <ExerciseTable exercises={exercises} />
            )
          ) : videos.length === 0 ? (
            <div className="flex-1">
              <p className="text-red-400">No videos found</p>
            </div>
          ) : (
            <VideoTable videos={videos} />
          )}
        </div>
      </div>


      <div className="fixed top-0 right-0 bottom-0 bg-slate-800 p-2">

        {type === "video" ? (
          <VideoForm handleSubmit={onSubmit} width="w-72" />
        ) : (
          <ExerciseUpload categoryId={categoryId} chapterId={chapterId} />
        )}
      </div>
    </div>
  );
};
