import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import EmptyStateSmall from "../../components/emptystates/EmptyStateSmall";
import Menu from "../../components/Menu";
import CardSkeleton from "../../components/skeleton/CardSkeleton";
import { deleteBook, fetchBooks } from "../../features/books/book-slice";
import { fetchSubjects } from "../../features/category/category-slice";
import { englishSubjects } from "../../utils";
import Book from "./Book";
import BookForm from "./BookForm";
const options = [
    { key: "edit", display: "Edit" },
    { key: "delete", display: "Delete" },
];
const languageOptions = [
    { id: "english", name: "English" },
    { id: "hindi", name: "Hindi" },
];

const classes = [
    { id: "1", name: "Class 1" },
    { id: "2", name: "Class 2" },
    { id: "3", name: "Class 3" },
    { id: "4", name: "Class 4" },
    { id: "5", name: "Class 5" },
    { id: "6", name: "Class 6" },
    { id: "7", name: "Class 7" },
    { id: "8", name: "Class 8" },
    { id: "9", name: "Class 9" },
    { id: "10", name: "Class 10" },
    { id: "11", name: "Class 11" },
    { id: "12", name: "Class 12" },
];
const Books = () => {
    const dispatch = useDispatch();
    const [showModal, setShowModal] = useState(false);
    const [bookToEdit, setBookToEdit] = useState();
    const [selectedClass, setSelectedClass] = useState(classes[0].id);
    const [selectedSubject, setSelectedSubject] = useState();
    const [selectedLanguage, setSelectedLanguage] = useState("english");
    const [subjectOptions, setSubjectOptions] = useState([]);
    const { books, bookFetchStatus } = useSelector((state) => state.book);
    const { subjects } = useSelector((state) => state.category);

    useEffect(() => {
        dispatch(fetchSubjects({ clas: selectedClass, flat: false }));
    }, [selectedClass]);

    useEffect(() => {
        dispatch(fetchBooks({ language: selectedLanguage, subject: selectedSubject }));
    }, [dispatch, selectedSubject, selectedLanguage]);

    useEffect(() => {
        if (subjects.length > 0) {
            const subjectOptions = subjects.map(({ _id, name }) => ({
                id: _id,
                name,
            }));
            const selectedKey = subjects[0]._id;
            setSubjectOptions(subjectOptions);
            setSelectedSubject(selectedKey);
        }
    }, [subjects]);

    const handleOptionClick = (option, bookId) => {
        if (option.key == "edit") {
            setBookToEdit(books.find((book) => book._id == bookId));
            setShowModal(true);
        }
        if (option.key == "delete") {
            dispatch(deleteBook(bookId));
        }
    };

    const subjectName = subjectOptions.find(option => option.id === selectedSubject)?.name
    const language = englishSubjects.some(subName => subName === subjectName) ? "english" : subjectName === "Hindi" ? "hindi" : selectedLanguage

    return (
        <>
            <div className="flex p-2 gap-4 items-center justify-between text-gray-700 bg-gray-50 rounded-lg border border-gray-200 dark:bg-gray-800 dark:border-gray-700 my-2 mx-2">
                <div className="flex gap-4">
                    <Menu
                        label="Choose Class :"
                        options={classes}
                        selectedKey={selectedClass}
                        onSelect={(key) => setSelectedClass(key)}
                        wrapperClasses="w-32"
                    />
                    <Menu
                        label="Choose Subject :"
                        options={subjectOptions}
                        selectedKey={selectedSubject}
                        onSelect={(key) => setSelectedSubject(key)}
                        wrapperClasses="w-48"
                    />
                </div>
                <Menu
                    options={languageOptions}
                    selectedKey={selectedLanguage}
                    onSelect={(key) => setSelectedLanguage(key)}
                    wrapperClasses="w-32"
                    label="Choose medium :"
                />
            </div>

            <div className="relative h-[85vh] p-2 overflow-auto">
                {bookFetchStatus !== "loading" &&
                    (!books || books.length == 0) &&
                    !showModal && (
                        <div className="w-full  h-full flex justify-center items-center">
                            <EmptyStateSmall
                                actionButtonText="Add new Book"
                                onActionClick={() => setShowModal(true)}
                                title="No books found!"
                                subTitle="You can create book by clicking Add new Button"
                            />
                        </div>
                    )}

                {bookFetchStatus == "loading" ? (
                    <CardSkeleton repeat={8} />
                ) : (
                    books.length > 0 && (
                        <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-4">
                            {books &&
                                books.map((book) => (
                                    <Book
                                        key={book._id}
                                        {...book}
                                        options={options}
                                        handleOptionClick={handleOptionClick}
                                    />
                                ))}
                        </div>
                    )
                )}

            </div>
            <BookForm
                book={bookToEdit}
                setShowModal={setShowModal}
                showModal={showModal}
                language={language}
                subject={selectedSubject}
                subjectName={subjectName}
            />
            <button
                type="button"
                onClick={() => {
                    setBookToEdit({});
                    setShowModal(true);
                }}
                className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-full text-sm p-2.5 text-center inline-flex items-center mr-2 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800 absolute bottom-2 right-2"
            >
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6"
                >
                    <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M12 4.5v15m7.5-7.5h-15"
                    />
                </svg>
                <span className="sr-only">Add Book</span>
            </button>
        </>
    );
};

export default Books;
