import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { axiosConfig } from "../../config/axios-config";

const initialState = {
  books: [],
  bookFetchStatus: "idle", // 'idle', | 'loading', | 'succeeded', | 'failed'
  bookFetchError: "",
  bookCreateStatus: "idle",
  bookDeleteError: "",
  bookCreateError: "",
};

export const createBook = createAsyncThunk(
  "book/createBook",
  async (bookData, { rejectWithValue }) => {
    try {
      const { data } = await axios.post("books", bookData, {
        ...axiosConfig,
      });
      return data.book;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const updateBook = createAsyncThunk(
  "book/updateBook",
  async ({ bookData, id }, { rejectWithValue }) => {
    try {
      const { data } = await axios.patch("book/" + id, bookData, {
        ...axiosConfig,
      });
      return data.book;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);
export const fetchBooks = createAsyncThunk(
  "book/fetchBooks",
  async (params) => {
    try {
      const { data } = await axios.get("books", {
        ...axiosConfig,
        params, // params: {days:30}
      });
      return data.books;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);

export const deleteBook = createAsyncThunk(
  "book/deleteBook",
  async (bookId, { rejectWithValue }) => {
    try {
      const { data } = await axios.delete("book/" + bookId, {
        ...axiosConfig,
      });
      return data.book;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const bookSlice = createSlice({
  name: "book",
  initialState,
  reducers: {},
  extraReducers: {
    // Fetch chapters
    [fetchBooks.pending]: (state) => {
      state.bookFetchStatus = "loading";
      state.bookFetchError = "";
    },
    [fetchBooks.fulfilled]: (state, action) => {
      state.books = action.payload;
      state.bookFetchStatus = "succeeded";
    },
    [fetchBooks.rejected]: (state, action) => {
      state.bookFetchStatus = "failed";
      state.books = [];
      state.bookFetchError = action.error.message;
    },

    [createBook.pending]: (state, action) => {
      state.bookCreateStatus = "loading";
    },
    [createBook.rejected]: (state, action) => {
      state.bookCreateStatus = "failed";
      state.bookCreateError = action.error.message;
    },
    [createBook.fulfilled]: (state, action) => {
      state.books.push(action.payload);
      state.bookCreateStatus = "succeeded";
    },

    [updateBook.pending]: (state, action) => {
      state.bookCreateStatus = "loading";
    },
    [updateBook.rejected]: (state, action) => {
      state.bookCreateStatus = "failed";
      state.bookCreateError = action.error.message;
    },
    [updateBook.fulfilled]: (state, action) => {
      state.books = state.books.map((book) =>
        book._id == action.payload._id ? action.payload : book
      );
      state.bookCreateStatus = "succeeded";
    },

    [deleteBook.fulfilled]: (state, action) => {
      state.books = state.books.filter(
        (book) => book._id !== action.payload._id
      );
    },

    [deleteBook.rejected]: (state, action) => {
      state.bookDeleteError = action.error;
    },
  },
});
export const selectBook = (state) => state.book.books;

export default bookSlice.reducer;
