import React from "react";
import { useState } from "react";
import { Modal } from "../modal/Modal";
import { useDispatch } from "react-redux";
import { deleteVideo, updateVideo } from "../../features/video/video-slice";
import { VideoForm } from "./VideoForm";
import { toast } from "react-toastify";
import ReactPlayer from 'react-player/youtube'

export const VideoRow = ({ video, expanded }) => {
  const { _id, title, videoId, order } = video;
  const [showModal, setShowModal] = useState(false);
  const [viewVideo, setViewVideo] = useState(false)
  const dispatch = useDispatch();
  const videoURL = `https://www.youtube.com/watch?v=${videoId}`
  const editClickHandler = async (video) => {
    try {
      await dispatch(updateVideo({ id: _id, video })).unwrap();
      setShowModal(false)
      toast.dark(`${video.title} updated successfully`)
    } catch (error) {
      console.log(error);
    }
  };

  const deleteVideoClickHandler = async()=>{
    try {
      await dispatch(deleteVideo(_id)).unwrap()
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <>
      <tr
        key={_id}
        className="bg-white border-b dark:bg-gray-800 dark:border-gray-700"
      >
        <td className="w-4 p-4">
          <div className="flex items-center">
            <input
              id="checkbox-table-1"
              type="checkbox"
              className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
            />
            <label htmlFor="checkbox-table-1" className="sr-only">
              checkbox
            </label>
          </div>
        </td>
        <td
          className="px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap"
        >
          <div className={expanded ? "flex flex-col gap-2 items-center" : "flex flex-col gap-2"}>
            <p>{title}</p>
            {
              expanded && <ReactPlayer url={videoURL} width="210px" height="160px" light />
            }
          </div>
        </td>
        <td
          className="px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap"
        >
          {videoId}
        </td>

        <td className="px-6 py-4 text-right">
          <button
            className="font-medium text-blue-600 dark:text-blue-500 hover:underline "
            onClick={() => setShowModal(true)}
          >
            Edit
          </button>
        </td>
        <td className="px-6 py-4 text-right">
          <button
            className="font-medium text-red-600 dark:text-red-500 hover:underline"
            onClick={deleteVideoClickHandler}
          >
            Delete
          </button>
        </td>
        <td className="px-6 py-4 text-right">
          <button
            className="font-medium text-blue-600 dark:text-blue-500 hover:underline "
            onClick={() => setViewVideo(true)}
          >
            Show
          </button>
        </td>
      </tr>
      {showModal && (
        <Modal show={showModal} toggle={setShowModal} title="Edit Video">
          <VideoForm handleSubmit={editClickHandler} video={{ title, videoId, order }} />
        </Modal>
      )}

      {
        viewVideo && <Modal show={viewVideo} toggle={setViewVideo} resetWidth={false} >
          <ReactPlayer url={videoURL} controls={true} style={{ padding: "8px" }} />
        </Modal>
      }
    </>
  );
};
