import { configureStore } from "@reduxjs/toolkit";
import categoryReducer from "../features/category/category-slice";
import chapterReducer from "../features/chapter/chapter-slice";
import exerciseReducer from "../features/exercise/exercise-slice";
import videoReducer from "../features/video/video-slice";
import authReducer from "../features/auth/auth-slice";
import notificationReducer from "../features/notification/notification-slice";
import bookReducer from "../features/books/book-slice";
import subjectReducer from "../features/subject/subject-slice";
export const store = configureStore({
  reducer: {
    category: categoryReducer,
    chapter: chapterReducer,
    exercise: exerciseReducer,
    video: videoReducer,
    auth: authReducer,
    notification: notificationReducer,
    book: bookReducer,
    subject: subjectReducer,
  },
});
