import { useDispatch } from "react-redux";
import { deleteChapter } from "../../features/chapter/chapter-slice";
import { TableHead } from "../table/TableHead";
import { ChapterRow } from "./ChapterRow";
import { toast } from "react-toastify"
export const ChapterTable = ({ chapters, breadcrumbsData }) => {
  const dispatch = useDispatch();
  const handleDeleteClick = async (id) => {
    try {
      await dispatch(deleteChapter(id)).unwrap()
    } catch (error) {
      toast.dark(error?.error)
    }
  };


  const columnData = [
    { field: "name", action: false },
    { field: "view", action: true },
    { field: "edit", action: true },
    { field: "delete", action: true },
    { field: "Premium", action: false },
  ];

  return (
    <div className="relative overflow-auto shadow-md sm:rounded-lg max-h-[90vh]">
      <table className="text-sm text-left text-gray-500 dark:text-gray-400 w-full">
        <TableHead columnData={columnData} />
        <tbody>
          {chapters.map((chapter) => {
            return (
              <ChapterRow key={chapter._id}
                chapter={chapter}
                handleDeleteClick={handleDeleteClick}
                breadcrumbsData={breadcrumbsData}
              />
            );
          })}
        </tbody>
      </table>
    </div>
  );
};
