import React from 'react'

const SubjectRow = ({ subject, handleDeleteClick, handleEditActionClick }) => {
    const { name, class: clas, _id, order, active, imageUrl, localizedName } = subject
    return (
        <>
            <tr
                key={_id}
                className="bg-white border-b dark:bg-gray-800 dark:border-gray-700"
            >
                <td className="w-4 p-4">
                    <div className="flex items-center">
                        <input
                            id="checkbox-table-1"
                            type="checkbox"
                            className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
                        />
                        <label htmlFor="checkbox-table-1" className="sr-only">
                            checkbox
                        </label>
                    </div>
                </td>
                <th
                    scope="row"
                    className="px-6 py-2 font-medium text-gray-900 dark:text-white whitespace-nowrap"
                >
                    {name} {localizedName ? ` (${localizedName})` : ""}
                </th>
                <td
                    scope="row"
                    className="px-6 py-2 font-medium text-gray-900 dark:text-white whitespace-nowrap"
                >
                    {clas}
                </td>


                <td
                    scope="row"
                    className="px-6 py-2 font-medium text-gray-900 dark:text-white whitespace-nowrap"
                >
                    {order}
                </td>
                <td
                    className={`px-6 py-2 first-letter:uppercase ${!active ? "text-slate-600" : "text-slate-300"}`}
                >
                    {active ? "Active" : "Inactive"}
                </td>

                <td className="px-6 py-2 text-right">
                    <button
                        className="font-medium text-white bg-green-500 dark:bg-green-600 px-4 py-2 rounded-md shadow-sm hover:underline"
                        onClick={() => handleEditActionClick(subject)}
                    >
                        Edit
                    </button>
                </td>

                <td className="px-6 py-2 text-right">
                    <button
                        className="font-medium text-white bg-red-500 dark:bg-red-600 px-4 py-2 rounded-md shadow-sm hover:underline"
                        onClick={() => handleDeleteClick(_id)}
                    >
                        Delete
                    </button>
                </td>
                <td
                    className="px-6 py-2 font-medium text-gray-900 dark:text-white whitespace-nowrap items-center "

                >
                    <img src={imageUrl} alt="subject" className='w-12 h-12 object-cover' />
                </td>
            </tr>
        </>
    )
}

export default SubjectRow