import { TableHead } from "../table/TableHead";
import { ExerciseRow } from "./ExerciseRow";
export const ExerciseTable = ({ exercises }) => {

  const columnData = [
    { field: "name", action: false },
    { field: "edit", action: true },
    { field: "download", action: true },
    { field: "delete", action: true },
    { field: "Premium", action: false },
  ];

  return (
    <table className=" text-sm text-left text-gray-500 dark:text-gray-400 w-full overflow-auto">
      <TableHead columnData={columnData} />
      <tbody>
        {exercises.map((exercise) => {
          return (
            <ExerciseRow key={exercise._id}
              exercise={exercise}
            />
          );
        })}
      </tbody>
    </table>

  );
};
