import React from "react";
import { useState } from "react";

import { Input } from "../inputs/Input";

export const VideoForm = ({ handleSubmit, video = {}, width = "" }) => {
  const [videoState, setVideoState] = useState({
    title: video.title || "",
    videoId: video.videoId || "",
    order: video.order || 0,
  });

  const { videoId, title, order } = videoState;

  const inputChangeHandler = (e) => {
    setVideoState({ ...videoState, [e.target.name]: e.target.value });
  };

  const saveHandler = (e) => {
    e.preventDefault();
    if (!title || !videoId) return;
    handleSubmit(videoState);
    setVideoState({ title: "", order: "", videoId: "" })
  };

  return (
    <form className={width + " shrink-0 p-2 flex flex-col"}>
      <Input
        id="title"
        value={title}
        label="Video title"
        placeholder="Title"
        handleChange={inputChangeHandler}
        name="title"
      />
      <Input
        id="videoId"
        value={videoId}
        label="Youtube video Id"
        placeholder="Youtube video Id"
        handleChange={inputChangeHandler}
        name="videoId"
      />
      <Input
        id="order"
        value={order}
        label="Order"
        placeholder="Order"
        handleChange={inputChangeHandler}
        name="order"
        min="0"
        type="number"
      />

      <button
        type="submit"
        onClick={saveHandler}
        className="w-full  my-2 text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
      >
        Save
      </button>
    </form>
  );
};
