import React, { useState } from "react";
import { Popup } from "../modal/Popup";
import { TableHead } from "../table/TableHead";
import { CategoryRow } from "./CategoryRow";
import { useDispatch } from "react-redux";
import { deleteCategory } from "../../features/category/category-slice";

export const CategoryTable = ({ categories, subjectId }) => {
  const [showPopup, setShowPopup] = useState(false);
  const [currentId, setCurrentId] = useState("");
  const dispatch = useDispatch();

  const columnData = [
    { field: "name", action: false },
    { field: "Status", action: false },
    { field: "Order", action: false },
    { field: "edit", action: true },
    { field: "view", action: true },
    { field: "delete", action: true },
    { field: "Premium", action: false },
  ];

  const handleDeletePopup = (id) => {
    setCurrentId(id);
    setShowPopup(true);
  };

  const handleDelete = async () => {
    await dispatch(deleteCategory(currentId)).unwrap();
    setShowPopup(false);
  };
  return (
    <>
      <table className="text-sm text-left text-gray-500 dark:text-gray-400 overflow-auto w-full">
        <TableHead columnData={columnData} />
        <tbody>
          {categories.map((category) => {
            return (
              <CategoryRow key={category._id}
                category={category}
                handleDeleteClick={handleDeletePopup}
                subjectId={subjectId}
              />
            );
          })}
        </tbody>
      </table>
      <Popup setShow={setShowPopup} show={showPopup} actionOk={handleDelete} title=" Are you sure you want to delete this category?" />
    </>
  );
};
