import { Fragment, useState } from 'react'
import { Dialog, Transition } from '@headlessui/react'
import {
    Bars3Icon,
    CalendarIcon,
    ChartBarIcon,
    FolderIcon,
    HomeIcon,
    InboxIcon,
    UsersIcon,
    BookOpenIcon,
    XMarkIcon,
} from '@heroicons/react/24/outline'
import { NavLink } from 'react-router-dom'


function classNames(...classes) {
    return classes.filter(Boolean).join(' ')
}

export default function SideNav({ children, navigation, handleLogout }) {
    const [sidebarOpen, setSidebarOpen] = useState(false)

    const [isSubmenuOpen, setIsSubMenuOpen] = useState(true)
    return (

        <div>
            <Transition.Root show={sidebarOpen} as={Fragment}>
                <Dialog as="div" className="relative z-40 lg:hidden" onClose={setSidebarOpen}>
                    <Transition.Child
                        as={Fragment}
                        enter="transition-opacity ease-linear duration-300"
                        enterFrom="opacity-0"
                        enterTo="opacity-100"
                        leave="transition-opacity ease-linear duration-300"
                        leaveFrom="opacity-100"
                        leaveTo="opacity-0"
                    >
                        <div className="fixed inset-0 bg-gray-600 bg-opacity-75" />
                    </Transition.Child>

                    <div className="fixed inset-0 z-40 flex">
                        <Transition.Child
                            as={Fragment}
                            enter="transition ease-in-out duration-300 transform"
                            enterFrom="-translate-x-full"
                            enterTo="translate-x-0"
                            leave="transition ease-in-out duration-300 transform"
                            leaveFrom="translate-x-0"
                            leaveTo="-translate-x-full"
                        >
                            <Dialog.Panel className="relative flex w-full max-w-xs flex-1 flex-col bg-gray-800">
                                <Transition.Child
                                    as={Fragment}
                                    enter="ease-in-out duration-300"
                                    enterFrom="opacity-0"
                                    enterTo="opacity-100"
                                    leave="ease-in-out duration-300"
                                    leaveFrom="opacity-100"
                                    leaveTo="opacity-0"
                                >
                                    <div className="absolute top-0 right-0 -mr-12 pt-2">
                                        <button
                                            type="button"
                                            className="ml-1 flex h-10 w-10 items-center justify-center rounded-full focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white"
                                            onClick={() => setSidebarOpen(false)}
                                        >
                                            <span className="sr-only">Close sidebar</span>
                                            <XMarkIcon className="h-6 w-6 text-white" aria-hidden="true" />
                                        </button>
                                    </div>
                                </Transition.Child>
                                <div className="h-0 flex-1 overflow-y-auto pt-5 pb-4">
                                    <div className="flex flex-shrink-0 items-center px-4">
                                        <img
                                            className="h-8 w-auto"
                                            src="https://www.thestudypath.com/wp-content/uploads/2021/01/The-Study-Path-website-Logo-New-SVG.svg"
                                            alt="Your Company"
                                        />
                                    </div>
                                    <nav className="mt-5 space-y-1 px-2">
                                        {navigation.map((item) => <NavItem key={item.name} item={item} isSubmenuOpen={isSubmenuOpen} setIsSubMenuOpen={setIsSubMenuOpen} />)}
                                    </nav>
                                </div>
                                <NavFooter handleLogout={handleLogout} />
                            </Dialog.Panel>
                        </Transition.Child>
                        <div className="w-14 flex-shrink-0">{/* Force sidebar to shrink to fit close icon */}</div>
                    </div>
                </Dialog>
            </Transition.Root>

            {/* Static sidebar for desktop */}
            <div className="hidden lg:fixed lg:inset-y-0 lg:flex lg:w-64 lg:flex-col">
                {/* Sidebar component, swap this element with another sidebar if you like */}
                <div className="flex min-h-0 flex-1 flex-col bg-gray-800">
                    <div className="flex flex-1 flex-col overflow-y-auto pt-5 pb-4">
                        <div className="flex flex-shrink-0 items-center justify-center px-4">
                            <img
                                className="h-16 w-auto"
                                src="https://www.thestudypath.com/wp-content/uploads/2021/01/The-Study-Path-website-Logo-New-SVG.svg"
                                alt="Your Company"
                            />
                        </div>
                        <nav className="mt-5 flex-1 space-y-1 px-2">
                            {navigation.map((item) => <NavItem key={item.name} item={item} isSubmenuOpen={isSubmenuOpen} setIsSubMenuOpen={setIsSubMenuOpen} />)}
                        </nav>
                        <NavFooter handleLogout={handleLogout} />
                    </div>
                </div>
            </div>
            <div className="flex flex-1 flex-col lg:pl-64">
                <div className="relative top-2 z-10 px-2 lg:hidden ">
                    <button
                        type="button"
                        className="p-1 inline-flex w-12 items-center justify-center rounded-md text-gray-500 hover:text-gray-700 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500"
                        onClick={() => setSidebarOpen(true)}
                    >
                        <span className="sr-only">Open sidebar</span>
                        <Bars3Icon className="h-6 w-6" aria-hidden="true" />
                    </button>
                </div>
                <main className="flex-1">
                    <div className="mx-auto max-w-7xl  h-screen overflow-hidden">
                        {children}
                    </div>
                </main>
            </div>

        </div>

    )
}


function NavItem({ item, depth, isSubmenuOpen, setIsSubMenuOpen }) {
    const _depth = depth || 0;
    if (!item) return null
    return <>
        {
            !item.navItems && <NavLink
                key={item.name}
                to={item.path}
                className={({ isActive }) => {
                    return classNames(
                        isActive
                            ? 'bg-gray-900 text-white'
                            : 'text-gray-300 hover:bg-gray-700 hover:text-white',
                        'group flex items-center py-2 text-base font-medium rounded-md', _depth == 0 ? "px-2 " : "px-8"
                    )
                }}
            >
                {
                    item.icon && <item.icon
                        className={classNames(
                            item.current ? 'text-gray-300' : 'text-gray-400 group-hover:text-gray-300',
                            'mr-4 flex-shrink-0 h-6 w-6'
                        )}
                        aria-hidden="true"
                    />
                }
                {item.name}
            </NavLink>
        }
        {
            item.navItems && <>
                <button type="button"
                    onClick={() => setIsSubMenuOpen(v => !v)}
                    className="px-2 py-2 flex items-center w-full text-base font-normal text-gray-900 rounded-lg transition duration-75 group hover:bg-gray-100 dark:text-white dark:hover:bg-gray-700" >
                    {item.icon && <item.icon
                        className={classNames(
                            item.current ? 'text-gray-300' : 'text-gray-400 group-hover:text-gray-300',
                            'mr-4 flex-shrink-0 h-6 w-6'
                        )}
                        aria-hidden="true"
                    />}
                    <span className="flex-1  text-left whitespace-nowrap" >{item.name}</span>
                    <svg className={"w-6 h-6 " + (!isSubmenuOpen ? "-rotate-45" : "")} fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd"></path></svg>
                </button>
                <div className={'px-6 ' + (!isSubmenuOpen ? "hidden" : "max-h-[20rem] overflow-y-auto")}>
                    {
                        item.navItems.map(item => {
                            return <NavItem key={item.name} item={item} depth={1 + _depth} {...{ isSubmenuOpen, setIsSubMenuOpen }} />
                        })
                    }
                </div>
            </>
        }
    </>
}

function NavFooter({ handleLogout }) {
    return <div className="flex flex-shrink-0 bg-gray-700 p-4">
        <div className="group block w-full flex-shrink-0">
            <div className="flex items-center">
                <div>
                    <img
                        className="inline-block h-9 w-9 rounded-full"
                        src="https://www.thestudypath.com/wp-content/uploads/2021/01/The-Study-Path-website-Logo-New-SVG.svg"
                        alt=""
                    />
                </div>
                <div className="ml-3">
                    <p className="text-sm font-medium text-white">Study Path</p>
                    <button className="text-xs font-medium text-gray-300 group-hover:text-gray-200" onClick={handleLogout}>Logout</button>
                </div>
            </div>
        </div>
    </div>
}