import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import { ButtonLoader } from '../../components/button/ButtonLoader';
import { Input } from '../../components/inputs/Input';
import { Modal } from '../../components/modal/Modal';
import { createBook, updateBook } from '../../features/books/book-slice';
import { isUrl } from '../../utils';

const BookForm = ({ book, setShowModal, showModal, language, subject, subjectName }) => {
    const [details, setDetails] = useState({})
    const [editingBookId, setEditingBookId] = useState()
    const { title = "", description = "", caption = "", ctaText = "", affiliateLink = "", image = null } = details;

    const { bookCreateStatus } = useSelector(state => state.book)
    const dispatch = useDispatch();
    const [imageUrl, setImageURL] = useState()

    const isFormCompleted = !!title && !!caption && !!affiliateLink && (!!image || !!imageUrl) && isUrl(affiliateLink)

    const inputChangeHandler = (e) => {
        if (e.target.type === "checkbox") {
            setDetails({ ...details, [e.target.name]: e.target.checked });
        } else
            setDetails({ ...details, [e.target.name]: e.target.value });
    };

    const fileChangeHandler = (e) => {
        if (e.target.files && e.target.files.length > 0) {
            const file = e.target.files[0]
            setDetails({ ...details, [e.target.name]: file })
            getSRCFromImageFIle(file)
        }
    }

    const saveHandler = async (e) => {
        e.preventDefault();
        if (!title) return;
        const _language = /english|hindi/gi.test(subjectName) ? "common" : language
        const formDetails = { title, description, image, caption, ctaText, affiliateLink, language: _language, subject }
        const formData = new FormData()

        for (const key in formDetails) {
            if (Object.hasOwnProperty.call(formDetails, key)) {
                const value = formDetails[key];
                if (value) {
                    formData.append(key, value)
                }
            }
        }
        try {
            if (editingBookId)
                await dispatch(updateBook({ bookData: formData, id: editingBookId })).unwrap()
            else await dispatch(createBook(formData)).unwrap()
            setDetails({})
            setImageURL(null)
            toast.success(editingBookId ? "Book updated successfully" : "Book created successfully")
            setShowModal(false)
        } catch (error) {
            toast.dark(error?.message)
        }
    };

    const getSRCFromImageFIle = (file) => {
        let reader = new FileReader();
        reader.onloadend = function () {
            setImageURL(reader.result)
        }
        reader.readAsDataURL(file);
    }

    const handleRemoveImage = () => {
        setImageURL(null);
        setDetails(details => ({ ...details, image: null }));
    }


    useEffect(() => {
        setDetails({ title: book?.title, description: book?.description, caption: book?.caption, ctaText: book?.ctaText, affiliateLink: book?.affiliateLink })
        setImageURL(book?.imageUrl)
        setEditingBookId(book?._id)
    }, [book])

    return <Modal
        show={showModal}
        title="Add a new affiliate book"
        resetWidth={false}
        maxWidth="w-full max-w-xl"
        toggle={setShowModal}
        onClose={() => setDetails({})}
    >
        <form className="w-full shrink-0 p-2">
            <Input
                id="title"
                value={title}
                label="Title"
                placeholder="Name"
                handleChange={inputChangeHandler}
                name="title"
            />

            <div >
                <p>Description</p>
                <textarea onChange={inputChangeHandler} name="description" rows={2} className='bg-gray-700 text-gray-100 w-full rounded-md' value={description} />
                {/* {description} */}
                {/* </textarea> */}
            </div>

            <Input
                id="caption"
                value={caption}
                label="Caption"
                placeholder="Caption"
                handleChange={inputChangeHandler}
                name="caption"
            />

            <Input
                id="ctaText"
                value={ctaText}
                label="CTA Button Text"
                placeholder="Enter cta text, default value is Buy on Amazon"
                handleChange={inputChangeHandler}
                name="ctaText"
            />

            <Input
                id="affiliateLink"
                value={affiliateLink}
                label="Affiliate Link"
                placeholder="Add affiliate link"
                handleChange={inputChangeHandler}
                name="affiliateLink"
            />

            {
                !!imageUrl == true ?
                    (<div className='relative border border-gray-600 p-1 rounded-md flex items-center justify-center max-h-52'>
                        <button className='p-0 text-gray-400  absolute top-1 right-1' type='button' onClick={handleRemoveImage}>
                            <svg
                                className="w-5 h-5"
                                fill="currentColor"
                                viewBox="0 0 20 20"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <path
                                    fillRule="evenodd"
                                    d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                                    clipRule="evenodd"
                                ></path>
                            </svg>
                        </button>
                        <img src={imageUrl} alt='book cover photo' className='max-h-48 w-auto object-cover' />
                    </div>) :
                    (<div className='my-2'>
                        <label
                            className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300"
                            htmlFor="icon"
                        >
                            Upload Image
                        </label>
                        <input
                            className="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50 dark:text-gray-400 focus:outline-none dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400"
                            aria-describedby="icon"
                            id="image"
                            type="file"
                            multiple={false}
                            name="image"
                            accept=".jpeg,.jpg,.png,"
                            onChange={fileChangeHandler}
                        />
                    </div>)
            }
            <div className='py-2'>
                <ButtonLoader clickHandler={saveHandler} text="Save" loading={bookCreateStatus === "loading"} disabled={bookCreateStatus === "loading" || !isFormCompleted} />
            </div>
        </form>
    </Modal>



}

export default BookForm