import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { axiosConfig } from "../../config/axios-config";

const initialState = {
  user: {},
  isLoggedIn: false,
  loginError: "",
  loginStatus: "idle",
  signUpStatus: "idle",
  signUperror: "",
};

export const login = createAsyncThunk(
  "auth/login",
  async (user, { rejectWithValue }) => {
    try {
      const { data } = await axios.post("/login", user, axiosConfig);
      if (data.success) {
        return data.user;
      }
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const logout = createAsyncThunk("auth/logout", async () => {
  localStorage.removeItem("token");
  try {
    const { data } = await axios.get("/logout", axiosConfig);
    if (data.success) {
      return data.user;
    }
  } catch (error) {
    return Promise.reject(error);
  }
});

export const signup = createAsyncThunk(
  "auth/signup",
  async (user, { rejectWithValue }) => {
    try {
      const { data } = await axios.post("/signup", user, axiosConfig);
      if (data.success) {
        return data.user;
      }
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const getUser = createAsyncThunk("user/getUser", async (_,{rejectWithValue}) => {
  try {
    const { data } = await axios.get("/user/profile", axiosConfig);
    if (data.success) {
      return data.user;
    }
  } catch (error) {
    return  rejectWithValue(error?.response?.data);
  }
});

export const authState = createSlice({
  name: "auth",
  initialState,
  reducers: {},
  extraReducers: {
    [login.fulfilled]: (state, action) => {
      state.user = action.payload;
      state.isLoggedIn = true;
      state.loginStatus = "succeeded";
      state.loginError = "";
    },
    [login.rejected]: (state, action) => {
      state.loginStatus = "failed";
      state.loginError = action.payload.message;
    },
    [login.pending]: (state, action) => {
      state.loginStatus = "loading";
      state.loginError = "";
    },
    [logout.fulfilled]: (state, action) => {
      state.loginStatus = "idle";
      state.isLoggedIn = false;
      state.user = action.payload;
    },

    [signup.fulfilled]: (state, action) => {
      state.user = action.payload;
      state.isLoggedIn = true;
      state.signUpStatus = "succeeded";
      state.signUperror = "";
    },
    [signup.pending]: (state, action) => {
      state.signUpStatus = "loading";
      state.signUperror = "";
    },
    [signup.rejected]: (state, action) => {
      state.signUpStatus = "failed";
      state.signUperror = action.payload.message;
    },
    [getUser.fulfilled](state,action){
      state.user = action.payload;
      state.isLoggedIn = true;
      state.loginStatus = "succeeded";
      state.loginError = "";
    },
  },
});

export default authState.reducer;
