import React from "react";
import { useEffect } from "react";

import { useDispatch, useSelector } from "react-redux";
import EmptyStateSmall from "../../components/emptystates/EmptyStateSmall";
import CheckBox from "../../components/inputs/CheckBox";
import TableSkeleton from "../../components/skeleton/TableSkeleton";
import { TableHead } from "../../components/table/TableHead";
import {
  deleteNotification,
  fetchNotifications,
} from "../../features/notification/notification-slice";
import { timeSince } from "../../utils/datetime";
import NotificationForm from "./NotificationForm";

export const Updates = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchNotifications({ days: 30 }));
  }, [dispatch]);

  const { notifications, notificationFetchStatus } = useSelector(
    (state) => state.notification
  );

  const columnData = [
    { field: "Title", action: false },
    { field: "Description", action: false },
    { field: "Time", action: false },
    { field: "delete", action: true },
  ];

  const handleDeleteClick = (id) => {
    dispatch(deleteNotification(id));
  };

  return (
    <div className="flex p-2 gap-4">
      {notificationFetchStatus == "loading" ? (
        <TableSkeleton />
      ) : (
        <div className="w-[calc(100%-19rem)]  overflow-auto h-full">
          {notifications?.length > 0 ? (
            <table className="w-full text-sm text-left text-gray-500 dark:text-gray-400">
              <TableHead columnData={columnData} />
              <tbody>
                {notifications.map((notification, index) => {
                  const { title, description, createdAt, _id } = notification;
                  return (
                    <tr
                      key={index}
                      className="bg-white border-b dark:bg-gray-800 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-600"
                    >
                      <th className="px-4">
                        {" "}
                        <CheckBox />
                      </th>
                      <td className="py-4 px-6 max-w-lg truncate text-center">
                        {title}
                      </td>
                      <td className="py-4 px-6 max-w-lg truncate text-center">
                        {description}
                      </td>
                      <td className="py-4 px-6 text-center">
                        {timeSince(createdAt)}
                      </td>
                      <td className="px-6 py-4 text-right">
                        <button
                          className="font-medium text-white bg-red-500 dark:bg-red-600 px-4 py-2 rounded-md shadow-sm hover:underline"
                          onClick={() => handleDeleteClick(_id)}
                        >
                          Delete
                        </button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          ) : (
            <div className="h-full flex items-center justify-center">
              <EmptyStateSmall
                title="No updates found!"
                subTitle="Create new updates or notification."
              />
            </div>
          )}
        </div>
      )}
      <NotificationForm />
    </div>
  );
};

export default Updates;
