import React, { useEffect } from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, useSearchParams } from "react-router-dom";
import { CategoryForm } from "../../components/category/CategoryForm";
import { CategoryTable } from "../../components/category/CategoryTable";
import Menu from "../../components/Menu";
import {
  createCategory,
  fetchCategories,
  fetchSubjects,
  selectCategory,
} from "../../features/category/category-slice";

const englishSubjects = [
  "First Flight",
  "Footprints Without Feet",
  "Beehive",
  "Moments",
  "Honeydew",
  "It So Happened",
  "Honeycomb",
  "An Alien Hand",
  "Honeysuckle",
  "A Pact with the Sun",
];
export const Category = () => {
  const dispatch = useDispatch();
  const [selectedSubject, setSelectedSubject] = useState({})
  const [searchParams, setSearchParams] = useSearchParams();
  const [subjects, setSubjects] = useState([])
  const { pathname } = useLocation();
  const clas = pathname.split("/").at(-1);

  const { subjects: allSubjects, } = useSelector(state => state.subject)

  const subjectName = selectedSubject?.name
  const [language, setLanguage] = useState(searchParams.get("lang"));
  const categories = useSelector(selectCategory).filter(category => {
    if (language == null || category.language == "common") return true;
    return category.language === language
  });

  const options = [
    { id: "english", name: "English" },
    { id: "hindi", name: "Hindi" },
  ]

  useEffect(() => {
    if (clas && language && subjectName) {
      dispatch(
        fetchCategories({
          clas,
          language,
          subject: subjectName
        })
      );
    }
  }, [dispatch, clas, language, subjectName]);

  useEffect(() => {
    setLanguage(searchParams.get("lang"))
    if (allSubjects?.length > 0) {
      const subjects = allSubjects?.filter(subject => subject.class === clas) || []
      if (subjects?.length > 0) {
        const subjectId = searchParams.get('subjectId')
        const selectedSubject = subjectId ? subjects.find(subject => subject._id === subjectId) : subjects[0]
        setSubjects(subjects)
        setSelectedSubject(selectedSubject)
        updateQueryString("subjectId", selectedSubject._id)
      }
    }

  }, [allSubjects, clas])

  const updateQueryString = (key, value) => {
    const oldQuery = searchParams.toString()
    const updatedSearchParams = new URLSearchParams(oldQuery)
    updatedSearchParams.set(key, value)
    setSearchParams(updatedSearchParams)
  }

  const addCategoryHandler = (category) => {
    const _language = (englishSubjects.includes(subjectName) || /\benglish\b/gi.test(subjectName)) ? "english" : /\bhindi\b/gi.test(subjectName) ? "hindi" : language
    dispatch(createCategory({ ...category, subject: selectedSubject?._id, clas, language: _language }));
  };

  const handleSelect = (selectedLanguages) => {
    updateQueryString("lang", selectedLanguages)
    setLanguage(selectedLanguages)
  }

  const handleSelectSubject = (subject) => {
    setSelectedSubject(subject)
    updateQueryString("subjectId", subject._id)
    // localStorage.setItem("subject", JSON.stringify(subject))
  }


  return (
    <div className="flex pl-2  gap-2 items-centers h-full">
      <div className="max-w-[calc(100%-19rem)]  overflow-auto h-full w-full">
        <div className="flex gap-4 sticky left-0 items-center top-0 py-2 border border-slate-800 my-2 px-2 bg-white dark:bg-black">

          <ul className="flex flex-wrap gap-2 items-center">
            {subjects.map((subject) => {
              return (
                <button
                  onClick={() => handleSelectSubject(subject)}
                  key={subject._id}
                  id={`${subject._id === selectedSubject?._id ? "active" : ""}`}
                  className="flex items-center px-4 py-1 text-base text-gray-900 bg-gray-50 rounded-lg hover:bg-gray-100 group hover:shadow dark:bg-gray-800 dark:hover:bg-gray-500 dark:text-white "
                >
                  <span className="flex-1 whitespace-nowrap">{subject?.name}</span>
                </button>
              );
            })}
          </ul>
        </div>
        {categories.length === 0 ? <div className="flex flex-1 h-[70vh] items-center justify-center ">
          <div>
            <p className="text-xl">No category found!</p>
            <p>Add new category for <span className="font-bold text-gray-600"> {selectedSubject?.name}</span>  and <span className="font-bold text-gray-600">{language}</span> medium.</p>
          </div>
        </div> : <CategoryTable categories={categories} subjectId={selectedSubject?._id} />}
      </div>

      <div className="w-72 shrink-0 p-4 shadow-lg bg-slate-800 fixed top-0 right-0 bottom-0 h-full">
        <div className="bg-gray-700 p-4 my-4 rounded-md w-full">
          <Menu options={options} onSelect={handleSelect} selectedKey={language} wrapperClasses="w-full" label="Medium" buttonClasses="w-full" />
        </div>
        <CategoryForm handleSubmit={addCategoryHandler} />
      </div>
    </div>
  );
};
