
import React from 'react'
import {
    FolderIcon,
    HomeIcon,
    InboxIcon,
    BookOpenIcon,
    ListBulletIcon
} from '@heroicons/react/24/outline'
import SideNav from '../components/sidebar/SideNav';
import { useDispatch, useSelector } from 'react-redux';
import { logout } from '../features/auth/auth-slice';
import { getDistinctClasses } from '../utils';
import { useState } from 'react';
import { useEffect } from 'react';
import { fetchAllSubjects } from '../features/subject/subject-slice';

const navigation = [
    { path: "/dashboard", name: "Dashboard", icon: HomeIcon },
    { path: "/books", name: "Affiliate Books", icon: BookOpenIcon },
    { path: "/updates", name: "Updates", icon: InboxIcon },
    { path: "/subjects", name: "Subjects", icon: ListBulletIcon },
    {
        path: "/content", name: "Content", icon: FolderIcon, navItems: [
            { path: "/content/6?lang=english", name: "Class 6" },
            { path: "/content/7?lang=english", name: "Class 7" },
            { path: "/content/8?lang=english", name: "Class 8" },
            { path: "/content/9?lang=english", name: "Class 9" },
            { path: "/content/10?lang=english", name: "Class 10" },
        ]
    },
];

const DashboardLayout = ({ children }) => {
    const [navItems, setNavItems] = useState(navigation)
    const dispatch = useDispatch()
    const _logout = () => dispatch(logout())
    const { subjects } = useSelector(state => state.subject)
    const classes = getDistinctClasses(subjects)

    useEffect(() => {
        dispatch(fetchAllSubjects({ clas: "all" }))
    }, [])

    useEffect(() => {
        if (subjects && subjects.length > 0) {
            console.log(classes);
            const navItems = classes.map(clas =>
                ({ path: `/content/${clas.id}?lang=english`, name: `Class ${clas.id}` })
            )
            setNavItems(_navItems => _navItems.map(item => {
                if (item.path === "/content") {
                    return { ...item, navItems }
                } else return item
            }))
        }
    }, [subjects])
    return (
        <SideNav navigation={navItems} handleLogout={_logout} >
            {children}
        </SideNav>
    )
}

export default DashboardLayout