import React from "react";
import { useEffect } from "react";
import { useLocation, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { fetchChapters } from "../../features/chapter/chapter-slice";
import { ChapterTable } from "../../components/chapter/ChapterTable";
import { ChapterForm } from "../../components/chapter/ChapterForm";
import { ChapterUploadForm } from "../../components/chapter/ChapterUploadForm";
import { BreadCrumb } from "../../components/breadcrumbs/BreadCrumb";
import { BreadCrumbHome } from "../../components/breadcrumbs/BreadCrumbHome";
import { BreadCrumbLink } from "../../components/breadcrumbs/BreadCrumbLink";
import { BreadCrumbCurrentPage } from "../../components/breadcrumbs/BreadCrumbCurrentPage";
import { getCategory } from "../../features/category/category-slice";
export const Chapter = () => {
  const dispatch = useDispatch();
  const { categoryId, clas } = useParams();

  const { category } = useSelector(state => state.category)

  const { hasExercise, subject, name, language } = category || {}

  const { chapters } = useSelector((state) => state.chapter);

  useEffect(() => {
    dispatch(fetchChapters({ categoryId }));
    dispatch(getCategory({ id: categoryId }))
  }, [categoryId, dispatch]);

  return (
    <div className="flex gap-2">
      <div className="flex flex-col gap-2 w-[calc(100%-18rem)] p-2">
        <BreadCrumb>
          <BreadCrumbHome title={clas} />
          <BreadCrumbLink title={subject?.name} path={`/content/${clas}?lang=${language}&subjectId=${subject?._id}`} />
          <BreadCrumbCurrentPage title={name} />
        </BreadCrumb>

        {chapters.length !== 0 ? (
          <ChapterTable
            chapters={chapters}
            breadcrumbsData={{ subject, clas, categoryName: name, lang: language }}
          />
        ) : (
          <div className="flex-1">
            <p className="text-red-400">No Chapters found</p>
          </div>
        )}
      </div>
      <div className="w-72 shrink-0 fixed top-0 right-0 bottom-0 bg-slate-800 p-2">
        {hasExercise ? (
          <div>
            <h3 className="p-2 text-xl ">Add Chapter</h3>
            <ChapterForm categoryId={categoryId} />
          </div>
        ) : (
          <ChapterUploadForm categoryId={categoryId} />
        )}
      </div>
    </div>
  );
};
