import React from "react";
import { useState } from "react";
import { Input } from "../inputs/Input";
import { RadioInput } from "../inputs/RadioInput";
import { ButtonLoader } from "../button/ButtonLoader";

export const ExerciseUpdateForm = ({ exercise, handleSubmit }) => {
  const [radioState, setRadioState] = useState(
    exercise.isPaid ? "premium-yes" : "premium-no"
  );
  const [file, setFile] = useState(null);

  const [exerciseState, setExerciseState] = useState({
    name: exercise.name || "",
    isPaid: exercise.isPaid || false,
  });

  const inputChangeHandler = (e) => {
    if (e.target.type === "radio") {
      setRadioState(e.target.id);
      setExerciseState({
        ...exerciseState,
        [e.target.name]: e.target.value === "true" ? true : false,
      });
    } else
      setExerciseState({ ...exerciseState, [e.target.name]: e.target.value });
  };
  const fileChangeHandler = (e) => {
    setFile(e.target.files[0]);
  };

  const updateHandler = (e) => {
    e.preventDefault();
    const formData = new FormData();
    for (const key in exerciseState) {
      formData.append(key, exerciseState[key]);
    }
    if (file) formData.append("file", file);
    handleSubmit(formData);
  };

  return (
    <form>
      <Input
        id="name"
        value={exerciseState.name}
        label="Exercise Name"
        placeholder="Name"
        handleChange={inputChangeHandler}
        name="name"
      />
      <div className="flex gap-2 mb-2 items-center">
        <span className="block text-sm font-medium text-gray-900 dark:text-gray-400">
          Premium ?
        </span>
        <RadioInput
          id="premium-yes"
          name="isPaid"
          value={true}
          label="Yes"
          checked={radioState === "premium-yes"}
          handleChange={inputChangeHandler}
        />
        <RadioInput
          id="premium-no"
          name="isPaid"
          value={false}
          label="No"
          checked={radioState === "premium-no"}
          handleChange={inputChangeHandler}
        />
      </div>
      <div>
        <label
          className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300"
          htmlFor="exercise"
        >
          Upload file
        </label>
        <input
          className="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50 dark:text-gray-400 focus:outline-none dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400"
          aria-describedby="exercise"
          id="exercise"
          type="file"
          onChange={fileChangeHandler}
        />
      </div>
      <ButtonLoader text="Save" clickHandler={updateHandler} />
    </form>
  );
};
