
import React from 'react'
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import { ButtonLoader } from '../../components/button/ButtonLoader';
import CheckBox from '../../components/inputs/CheckBox';
import { Input } from '../../components/inputs/Input';
import { createNotification } from '../../features/notification/notification-slice';
import { Modal } from '../../components/modal/Modal';
import Editor from '../../components/editor/editor';
import { getStyledHTML } from '../../utils/htmlMarkup';
const NotificationForm = ({ notification = {} }) => {

    const [details, setDetails] = useState({ title: notification.title, description: notification.description })

    const { title, description, isPushNotification, icon, thumbnail, } = details;

    const { notificationCreateStatus } = useSelector(state => state.notification)
    const dispatch = useDispatch();
    const [contents, setContents] = useState({})
    const [showModal, setShowModal] = useState(false)


    const inputChangeHandler = (e) => {
        if (e.target.type === "checkbox") {
            setDetails({ ...details, [e.target.name]: e.target.checked });
        } else
            setDetails({ ...details, [e.target.name]: e.target.value });
    };

    const fileChangeHandler = (e) => {
        if (e.target.files && e.target.files.length > 0) {
            const file = e.target.files[0]
            setDetails({ ...details, [e.target.name]: file })
        }
    }

    const saveHandler = async (e) => {
        e.preventDefault();
        if (!title) return;
        const topics = Object.keys(details).filter(key => key.startsWith("class_") && details[key] === true);
        const html = contents["html"] ? getStyledHTML(contents["html"], details["description"], details["header"]) : null

        const formDetails = { title, description, icon, isPushNotification, thumbnail, topics, html, json: JSON.stringify(contents["json"]) }
        const formData = new FormData()

        for (const key in formDetails) {
            if (Object.hasOwnProperty.call(formDetails, key)) {
                const value = formDetails[key];
                if (value && key == "topics") {
                    topics.forEach(topic => formData.append("topics", topic))
                } else if (value) {
                    formData.append(key, value)
                }
            }
        }
        try {
            await dispatch(createNotification(formData)).unwrap()
            setDetails({})
            setContents({})
            toast.success("Notification created successfully")
        } catch (error) {
            toast.dark(error?.message)
        }
    };

    return <>
        <form className="w-72 shrink-0 p-4 shadow-lg bg-slate-800 fixed top-0 right-0 bottom-0 h-full">
            <Input
                id="title"
                value={title}
                label="Title"
                placeholder="Name"
                handleChange={inputChangeHandler}
                name="title"
            />
            <div >
                <p>Description</p>
                <textarea onChange={inputChangeHandler} name="description" rows={4} className='bg-gray-700 text-gray-100 w-full rounded-md'  >
                    {description}
                </textarea>
            </div>
            <div className='my-2'>
                <label
                    className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300"
                    htmlFor="icon"
                >
                    Upload icon
                </label>
                <input
                    className="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50 dark:text-gray-400 focus:outline-none dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400"
                    aria-describedby="icon"
                    id="icon"
                    type="file"
                    multiple={false}
                    name="icon"
                    accept=".jpeg,.jpg,.png,"
                    onChange={fileChangeHandler}
                />
            </div>
            <div className='my-2'>
                <label
                    className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300"
                    htmlFor="thumbnail"
                >
                    Upload Thumbnail
                </label>
                <input
                    className="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50 dark:text-gray-400 focus:outline-none dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400"
                    aria-describedby="icon"
                    id="thumbnail"
                    type="file"
                    multiple={false}
                    name="thumbnail"
                    accept=".jpeg,.jpg,.png,"
                    onChange={fileChangeHandler}
                />
            </div>


            <div className='flex gap-2 flex-wrap border border-gray-700 rounded-md p-2 my-2 '>
                <CheckBox onChange={inputChangeHandler} name="class_6" label="Class 6" />
                <CheckBox onChange={inputChangeHandler} name="class_7" label="Class 7" />
                <CheckBox onChange={inputChangeHandler} name="class_8" label="Class 8" />
                <CheckBox onChange={inputChangeHandler} name="class_9" label="Class 9" />
                <CheckBox onChange={inputChangeHandler} name="class_10" label="Class 10" />
                <CheckBox onChange={inputChangeHandler} name="class_all" label="All" />
            </div>
            <CheckBox onChange={inputChangeHandler} isChecked={isPushNotification} value={isPushNotification} name="isPushNotification" label="Send push notification" />

            <ButtonLoader text="Add contents" clickHandler={() => setShowModal(true)} type="button" />
            <div className='py-2'>
                <ButtonLoader clickHandler={saveHandler} text="Save" loading={notificationCreateStatus === "loading"} disabled={notificationCreateStatus === "loading" || !title} />
            </div>
        </form>

        <Modal show={showModal} title="Editor" resetWidth={false} toggle={setShowModal}>
            <div className='bg-white overflow-auto h-[80vh]'>
                <Editor onEdit={(json, html) => setContents({ html, json })} />
            </div>
        </Modal>

    </>

}

export default NotificationForm