import React from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { createChapter } from "../../features/chapter/chapter-slice";
import { ButtonLoader } from "../button/ButtonLoader";
import { Input } from "../inputs/Input";
import { RadioInput } from "../inputs/RadioInput";
import { toast } from "react-toastify"

export const ChapterForm = ({ categoryId }) => {
  const [chapterState, setChapterState] = useState({
    name: "",
    isPaid: false,
  });

  const { name, isPaid } = chapterState;
  const [radioState, setRadioState] = useState({
    isPaid: isPaid ? "premium-yes" : "premium-no",
  });

  const { chapterCreateStatus } = useSelector(state => state.chapter)
  const dispatch = useDispatch();

  const inputChangeHandler = (e) => {
    if (e.target.type === "radio") {
      setRadioState({ ...radioState, isPaid: e.target.id });
      setChapterState({
        ...chapterState,
        [e.target.name]: e.target.value === "true" ? true : false,
      });
    } else
      setChapterState({ ...chapterState, [e.target.name]: e.target.value });
  };

  const saveHandler = async (e) => {
    e.preventDefault();
    if (!name) return;
    try {
      await dispatch(createChapter({ categoryId, ...chapterState })).unwrap();
      setChapterState({ name: "", isPaid: false })
      toast.success("Chapter created successfully")
    } catch (error) {
      toast.dark(error?.message)
    }
  };

  return (
    <form className="p-2">
      <Input
        id="name"
        value={name}
        label="Chapter Name"
        placeholder="Name"
        handleChange={inputChangeHandler}
        name="name"
      />
      <div className="flex gap-2 mb-6 items-center">
        <span className="block text-sm font-medium text-gray-900 dark:text-gray-400">
          Premium ?
        </span>
        <RadioInput
          id="premium-yes"
          name="isPaid"
          value={true}
          label="Yes"
          checked={radioState.isPaid === "premium-yes"}
          handleChange={inputChangeHandler}
        />
        <RadioInput
          id="premium-no"
          name="isPaid"
          value={false}
          label="No"
          checked={radioState.isPaid === "premium-no"}
          handleChange={inputChangeHandler}
        />
      </div>
      <ButtonLoader clickHandler={saveHandler} text="Save" status={chapterCreateStatus === "loading"} disabled={chapterCreateStatus === "loading" || !chapterState.name} />
    </form>
  );
};
