import React from 'react'
import { useState } from 'react'

const Search = ({ initialSearchText = "", handleSearch, placeholder }) => {
    const [text, setText] = useState(initialSearchText)
    const handleInputChange = (e) => {
        setText(e.target.value)
        if (handleSearch) handleSearch(e.target.value)
    }
    return (
        <div className="relative">
            <label htmlFor="table-search" className="sr-only">Search</label>
            <svg className="w-5 h-5 text-gray-500 dark:text-gray-400 absolute translate-y-1/2 left-2" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd"></path></svg>
            <input type="search" className="block p-2 pl-10 text-sm text-gray-900 border border-gray-300 rounded-lg w-80 bg-gray-50 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder={placeholder} onChange={handleInputChange} value={text} />
        </div>
    )
}

export default Search