import React from 'react'
import Dropdown from '../../components/dropdown'

const Book = ({
    _id,
    title,
    imageUrl,
    caption,
    ctaText,
    description,
    affiliateLink,
    options,
    handleOptionClick
}) => {
    return (
        <div
            className="flex bg-white border rounded-lg shadow-md  hover:bg-gray-100 dark:border-gray-700 dark:bg-gray-800 dark:hover:bg-gray-700 relative"
        >
            <img
                className="object-cover w-36  m-4"
                src={imageUrl}
                alt={title}
            />
            <div className="py-4 leading-normal w-1/2 flex flex-col gap-2 pr-6">
                <h5 className="text-lg font-bold tracking-tight text-gray-900 dark:text-white">
                    {title}
                </h5>
                <p className=" font-normal text-gray-700 dark:text-gray-400 line-clamp-4 max-w-fit">
                    {description}
                </p>

                <div className="flex gap-2 items-center">
                    <svg
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                        strokeWidth={1.5}
                        stroke="currentColor"
                        className="w-4 h-4"
                    >
                        <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            d="M11.25 11.25l.041-.02a.75.75 0 011.063.852l-.708 2.836a.75.75 0 001.063.853l.041-.021M21 12a9 9 0 11-18 0 9 9 0 0118 0zm-9-3.75h.008v.008H12V8.25z"
                        />
                    </svg>
                    {caption}
                </div>
                <a
                    href={affiliateLink}
                    target="_blank"
                    rel="noreferrer"
                    className="inline-flex items-center justify-center px-4 py-2 text-sm font-medium text-center text-gray-900 bg-white border border-gray-300 rounded-lg hover:bg-gray-100 focus:ring-4 focus:outline-none focus:ring-gray-200 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-800 dark:hover:border-gray-800 dark:focus:ring-gray-700"
                >
                    {ctaText}
                </a>
            </div>
            <div className="absolute top-1 right-2">
                <Dropdown
                    options={options}
                    onOptionClick={(option) => handleOptionClick(option, _id)}
                    position="right"
                />
            </div>
        </div>
    )
}

export default Book