import { Fragment, useState } from 'react'
import { Combobox, Transition } from '@headlessui/react'
import { CheckIcon, ChevronUpDownIcon } from '@heroicons/react/20/solid'
import { useEffect } from 'react'


export default function ComboboxStyled({ options, onSelect, selectedKey, displayKey, label, wrapperClasses }) {
    const [selected, setSelected] = useState(options[0])
    const [query, setQuery] = useState('')

    const filteredPeople =
        query === ''
            ? options
            : options.filter((option) =>
                option.name
                    .toLowerCase()
                    .replace(/\s+/g, '')
                    .includes(query.toLowerCase().replace(/\s+/g, ''))
            )


    const handleSelect = (selected) => {
        setSelected(selected)
        if (onSelect) onSelect(selected.id)
    }

    const handleInputChange = (event) => {
        const value = event.target.value
        setQuery(value)
        if (options.some(option => option.id === value)) onSelect(value)
    }

    useEffect(() => {
        setSelected(options?.find(option => option.id === selectedKey))
    }, [selectedKey])

    const inputClasses = `shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light`
    return (
        // <div className="fixed top-16 w-72">
        <Combobox value={selected} onChange={handleSelect} by={(a, b) => a?.id === b?.id}>
            <Combobox.Label>{label}</Combobox.Label>
            <div className={(wrapperClasses ? wrapperClasses : "mt-1") + " relative"}>
                <div
                    className="relative w-full cursor-default overflow-hidden rounded-lg  focus-visible:ring-2 focus-visible:ring-white dark:focus-visible:ring-blue-500 focus-visible:ring-opacity-75 focus-visible:ring-offset-2 focus-visible:ring-offset-blue-500 sm:text-sm"
                >
                    <Combobox.Input
                        // className="w-full border-none py-2 pl-3 pr-10 text-sm leading-5 text-gray-900 focus:ring-0"

                        className={inputClasses}
                        displayValue={(option) => option[displayKey] || option.name}
                        onChange={handleInputChange}
                    />
                    <Combobox.Button className="absolute inset-y-0 right-0 flex items-center pr-2">
                        <ChevronUpDownIcon
                            className="h-5 w-5 text-gray-400 dark:text-white"
                            aria-hidden="true"
                        />
                    </Combobox.Button>
                </div>
                <Transition
                    as={Fragment}
                    leave="transition ease-in duration-100"
                    leaveFrom="opacity-100"
                    leaveTo="opacity-0"
                    afterLeave={() => setQuery('')}
                >
                    <Combobox.Options className="absolute mt-1 max-h-60 w-full overflow-auto rounded-md bg-white dark:bg-black py-1 text-base shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none sm:text-sm">
                        {filteredPeople.length === 0 && query !== '' ? (
                            <div className="relative cursor-default select-none py-2 px-4 text-gray-700">
                                Nothing found.
                            </div>
                        ) : (
                            filteredPeople.map((option) => (
                                <Combobox.Option
                                    key={option.id}
                                    className={({ active }) =>
                                        `relative cursor-default select-none py-2 pl-10 pr-4 ${active ? 'bg-teal-600 dark:bg-blue-500 text-white ' : 'text-gray-900 dark:text-white'
                                        }`
                                    }
                                    value={option}
                                >
                                    {({ selected, active }) => (
                                        <>
                                            <span
                                                className={`block truncate ${selected ? 'font-medium' : 'font-normal'
                                                    }`}
                                            >
                                                {option.name}
                                            </span>
                                            {selected ? (
                                                <span
                                                    className={`absolute inset-y-0 left-0 flex items-center pl-3 ${active ? 'text-white dark:text-black' : 'text-blue-600'
                                                        }`}
                                                >
                                                    <CheckIcon className="h-5 w-5" aria-hidden="true" />
                                                </span>
                                            ) : null}
                                        </>
                                    )}
                                </Combobox.Option>
                            ))
                        )}
                    </Combobox.Options>
                </Transition>
            </div>
        </Combobox>
        // </div>
    )
}
