import { useState } from "react";
import { TableHead } from "../table/TableHead";
import { VideoRow } from "./VideoRow";

export const VideoTable = ({ videos }) => {
  const [showAll, setShowAll] = useState(false)
  const columnData = [
    { field: "Title", action: false },
    { field: "Video Id", action: false },
    { field: "edit", action: true },
    { field: "delete", action: true },
    {
      field: "show", action: true, actionButton: <button
        className=" shadow-md rounded-md bg-gray-800 px-4 py-2"
        onClick={() => setShowAll(!showAll)}
      >
        {showAll ? "Hide Video" : "Show Video"}
      </button>
    },
  ];


  return (

    <table className="w-full text-sm text-left text-gray-500 dark:text-gray-400">
      <TableHead columnData={columnData} />
      <tbody>
        {videos.map((video) => {
          return <VideoRow key={video._id} video={video} expanded={showAll} />;
        })}
      </tbody>
    </table>

  );
};
