import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { axiosConfig } from "../../config/axios-config";

const initialState = {
  subjects: [],
  subjectFetchStatus: "idle", // 'idle', | 'loading', | 'succeeded', | 'failed'
  subjectCreateStatus: "idle",
  subjectFetchError: "",
  subjectCreateError: "",
  subject: {},
  subjectUpdateStatus: "idle",
  subjectUpdateError: "",
};

export const fetchAllSubjects = createAsyncThunk(
  "subjects/fetchAllSubjects",
  async (params, { rejectWithValue }) => {
    try {
      const { data } = await axios.get("subjects", {
        ...axiosConfig,
        params,
      });
      return data.subjects;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);
export const updateSubject = createAsyncThunk(
  "subjects/updateSubject",
  async ({ subject, _id }, { rejectWithValue }) => {
    try {
      const { data } = await axios.patch("subject/" + _id, subject, {
        ...axiosConfig,
      });
      return data.subject;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);
