export const deleteSubject = createAsyncThunk(
  "subjects/deleteSubject",
  async (subjectId, { rejectWithValue }) => {
    try {
      const { data } = await axios.delete("subject/" + subjectId, axiosConfig);
      return data.subject;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const createSubject = createAsyncThunk(
  "subjects/createSubject",
  async (subject, { rejectWithValue }) => {
    try {
      const { data } = await axios.post("subjects", subject, axiosConfig);
      return data.subject;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const subjectSlice = createSlice({
  name: "subject",
  initialState,
  extraReducers: {
    // Fetch subjects
    [fetchAllSubjects.pending]: (state) => {
      state.subjectFetchStatus = "loading";
      state.subjectFetchError = "";
    },
    [fetchAllSubjects.fulfilled]: (state, action) => {
      state.subjects = action.payload;
      state.subjectFetchStatus = "succeeded";
    },
    [fetchAllSubjects.rejected]: (state, action) => {
      state.subjectFetchStatus = "failed";
      //   state.subjects = [];
      state.subjectFetchError = action.error.message;
    },

    // create subjects
    [createSubject.fulfilled]: (state, action) => {
      state.subjects = [action.payload, ...state.subjects];
      state.subjectCreateStatus = "succeeded";
      state.subjectCreateError = "";
    },

    [createSubject.pending]: (state, action) => {
      state.subjectCreateStatus = "loading";
      state.subjectCreateError = "";
    },
    [createSubject.rejected]: (state, action) => {
      state.subjectCreateStatus = "failed";
      state.subjectCreateError = action.error.message;
    },

    //Delete Subject
    [deleteSubject.fulfilled]: (state, action) => {
      state.subjects = state.subjects.filter(
        (subject) => subject._id !== action.payload._id
      );
    },

    // Update subject
    [updateSubject.pending]: (state, action) => {
      state.subjectUpdateStatus = "loading";
    },
    [updateSubject.rejected]: (state, action) => {
      state.subjectUpdateStatus = "failed";
      state.subjectUpdateError = action.error.message;
    },

    [updateSubject.fulfilled]: (state, action) => {
      state.subjectUpdateStatus = "succeeded";
      state.subjects = state.subjects.map((subject) =>
        subject._id === action.payload._id ? action.payload : subject
      );
    },
  },
});

export default subjectSlice.reducer;
