import React from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { ButtonLoader } from "../../components/button/ButtonLoader";
import { Input } from "../../components/inputs/Input";
import { signup } from "../../features/auth/auth-slice";
export const SignUp = () => {
  const [userData, setUserData] = useState({
    email: "",
    name: "",
    password: "",
  });
  const dispatch = useDispatch();
  const { signUpStatus: status, signUperror } = useSelector(
    (state) => state.auth
  );

  const inputChangeHandler = (e) => {
    setUserData((data) => ({ ...data, [e.target.name]: e.target.value }));
  };

  const navigate = useNavigate();

  const signupHandler = async (e) => {
    e.preventDefault();
    try {
      await dispatch(signup(userData)).unwrap();
      navigate("/content/6", { replace: true });
    } catch (error) {
      console.log(error);
    }
  };

  const { name, email, password } = userData;
  return (
    <div className="flex justify-center items-center max-h-screen h-screen">
      <form className="max-w-lg w-full">
        <Input
          placeholder="Email"
          label="Email"
          value={email}
          required={true}
          id="email"
          type="email"
          name="email"
          handleChange={inputChangeHandler}
        />
        <Input
          placeholder="Name"
          label="Name"
          value={name}
          required={true}
          id="name"
          name="name"
          handleChange={inputChangeHandler}
        />
        <Input
          placeholder="Password"
          label="Password"
          value={password}
          required={true}
          id="password"
          type="password"
          name="password"
          handleChange={inputChangeHandler}
        />
        <ButtonLoader
          clickHandler={signupHandler}
          loading={status === "loading"}
          text="Login"
          disabled={status === "loading" || !email || !password}
        />
        {signUperror && <p className="text-red-600">{signUperror}</p>}
      </form>
    </div>
  );
};
