import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { axiosConfig } from "../../config/axios-config";

const initialState = {
  categories: [],
  subjects: [],
  categoryStatus: "idle", // 'idle', | 'loading', | 'succeeded', | 'failed'
  subjectStatus: "idle",
  categoryError: "",
  subjectError: "",
  subject: {},
  categoryUpdateStatus: "idle",
  categoryUpdateError: "",
  category: {},
};

export const fetchCategories = createAsyncThunk(
  "categories/fetchCategories",
  async (params) => {
    try {
      const { data } = await axios.get("categories", {
        ...axiosConfig,
        params,
      });
      return data.categories;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);
export const updateCategory = createAsyncThunk(
  "categories/updateCategory",
  async (category) => {
    try {
      const { data } = await axios.patch("category/" + category._id, category, {
        ...axiosConfig,
      });
      return data.category;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);
export const getCategory = createAsyncThunk(
  "categories/getCategory",
  async ({ id }) => {
    try {
      const { data } = await axios.get("category/" + id, {
        ...axiosConfig,
      });
      return data.category;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);

export const deleteCategory = createAsyncThunk(
  "categories/deleteCategory",
  async (categoryId) => {
    try {
      const { data } = await axios.delete(
        "category/" + categoryId,
        axiosConfig
      );
      return data.category;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);
export const fetchSubjects = createAsyncThunk(
  "categories/fetchSubjects",
  async (params) => {
    try {
      const { data } = await axios.get("subjects", {
        ...axiosConfig,
        params,
      });

      return data.subjects;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);
export const createCategory = createAsyncThunk(
  "categories/createCategory",
  async (category) => {
    try {
      const { data } = await axios.post("categories", category, axiosConfig);

      return data.category;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);

export const categorySlice = createSlice({
  name: "category",
  initialState,
  reducers: {
    setSelectedSubject: (state, action) => {
      localStorage.setItem("subject", JSON.stringify(action.payload));
      state.subject = action.payload;
    },
  },
  extraReducers: {
    // Fetch categories
    [fetchCategories.pending]: (state) => {
      state.categoryStatus = "loading";
      state.categoryError = "";
    },
    [fetchCategories.fulfilled]: (state, action) => {
      state.categories = action.payload;
      state.categoryStatus = "succeeded";
    },
    [fetchCategories.rejected]: (state, action) => {
      state.categoryStatus = "failed";
      state.categories = [];
      state.categoryError = action.error.message;
    },
    // Create Category
    [createCategory.fulfilled]: (state, action) => {
      state.categories = state.categories.concat(action.payload);
    },

    // Fetch subjects
    [fetchSubjects.fulfilled]: (state, action) => {
      state.subjects = action.payload;
      state.categoryStatus = "succeeded";
      const subject = localStorage.getItem("subject");
      if (!subject)
        localStorage.setItem("subject", JSON.stringify(action.payload[0]));
    },
    [fetchSubjects.pending]: (state, action) => {
      state.subjectStatus = "loading";
      state.subjectError = "";
    },
    [fetchSubjects.rejected]: (state, action) => {
      state.subjectStatus = "failed";
      state.subjectError = action.payload;
    },
    //Delete Category
    [deleteCategory.fulfilled]: (state, action) => {
      state.categories = state.categories.filter(
        (category) => category._id !== action.payload._id
      );
    },

    // Update category
    [updateCategory.pending]: (state, action) => {
      state.categoryUpdateStatus = "loading";
    },
    [updateCategory.rejected]: (state, action) => {
      state.categoryUpdateStatus = "failed";
    },

    [updateCategory.fulfilled]: (state, action) => {
      state.categoryUpdateStatus = "succeeded";
      state.categories = state.categories.map((cat) =>
        cat._id === action.payload._id ? action.payload : cat
      );
    },

    [getCategory.fulfilled]: (state, action) => {
      state.category = action.payload;
    },
  },
});
export const { setSelectedSubject } = categorySlice.actions;
export const selectedSubject = (state) => state.category.subject;
export const selectCategory = (state) => state.category.categories;
export const selectSubject = (state) => state.category.subjects;
export const getCategoryError = (state) => state.category.error;
export const getCategoryStatus = (state) => state.category.categoryStatus;
export default categorySlice.reducer;
