import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { axiosConfig } from "../../config/axios-config";

const initialState = {
  chapters: [],
  chapterFetchStatus: "idle", // 'idle', | 'loading', | 'succeeded', | 'failed'
  chapterFetchError: "",
  chapterUpdateStatus: "idle",
  chapterUpdateError: "",
  chaptersCreateStatus: "idle",
  chapterCreateStatus: "idle",
  chapterDeleteError: "",
  chapterCreateError:""
};

export const createChapter = createAsyncThunk(
  "chapter/createChapter",
  async (chapterData, {rejectWithValue}) => {
    try {
      const { data } = await axios.post("chapter", chapterData, {
        ...axiosConfig,
      });
      return data.chapter;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);
export const createChapters = createAsyncThunk(
  "chapter/createChapters",
  async (chapterData) => {
    try {
      const { data } = await axios.post("chapters", chapterData, {
        ...axiosConfig,
      });
      return data.chapters;
    } catch (error) {
      return Promise.reject(error);
    }
  }
);

export const fetchChapters = createAsyncThunk(
  "chapter/fetchChapters",
  async (params) => {
    try {
      const { data } = await axios.get("chapters", {
        ...axiosConfig,
        params, // params: {categoryId}
      });
      return data.chapters;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);
export const updateChapter = createAsyncThunk(
  "chapter/updateChapter",
  async ({ id, chapter }, {rejectWithValue}) => {
    try {
      const { data } = await axios.patch("chapter/" + id, chapter, {
        ...axiosConfig,
      });
      return data.chapter;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);
export const deleteChapter = createAsyncThunk(
  "chapter/deleteChapter",
  async (chapterId, { rejectWithValue }) => {
    try {
      const { data } = await axios.delete("chapter/" + chapterId, {
        ...axiosConfig,
      });
      return data.chapter;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const chapterSlice = createSlice({
  name: "chapter",
  initialState,
  reducers: {},
  extraReducers: {
    // Fetch chapters
    [fetchChapters.pending]: (state) => {
      state.chapterFetchStatus = "loading";
      state.chapterFetchError = "";
    },
    [fetchChapters.fulfilled]: (state, action) => {
      state.chapters = action.payload;
      state.chapterFetchStatus = "secceeded";
    },
    [fetchChapters.rejected]: (state, action) => {
      state.chapterFetchStatus = "failed";
      state.chapters = [];
      state.chapterFetchError = action.error.message;
    },

    [createChapter.pending]: (state, action) => {
      state.chapterCreateStatus = "loading"
    },
    [createChapter.rejected]: (state, action) => {
      state.chapterCreateStatus = "failed"
      state.chapterCreateError = action.error.message
    },
    [createChapter.fulfilled]: (state, action) => {
      state.chapters.push(action.payload);
      state.chapterCreateStatus = "secceeded"
    },

    [createChapters.fulfilled]: (state, action) => {
      state.chapters = state.chapters.concat(action.payload);
      state.chaptersCreateStatus = "secceeded";
    },

    [createChapters.pending]: (state, action) => {
      state.chaptersCreateStatus = "loading";
    },
    [createChapters.rejected]: (state, action) => {
      state.chaptersCreateStatus = "failed";
    },

    [deleteChapter.fulfilled]: (state, action) => {
      state.chapters = state.chapters.filter(
        (chapter) => chapter._id !== action.payload._id
      );

    },

    [deleteChapter.rejected]: (state, action) => {
      state.chapterDeleteError = action.error
    },

    // Update chapter
    [updateChapter.pending]: (state, action) => {
      state.chapterUpdateStatus = "loading";
    },
    [updateChapter.rejected]: (state, action) => {
      state.chapterUpdateStatus = "failed";
      state.chapterUpdateError = action.error;
    },

    [updateChapter.fulfilled]: (state, action) => {
      state.chapterUpdateStatus = "succeeded";
      state.chapters = state.chapters.map((chapter) =>
        chapter._id === action.payload._id ? action.payload : chapter
      );
      state.chapterUpdateError = "";
    },
  },
});

export default chapterSlice.reducer;
