import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { axiosConfig } from "../../config/axios-config";

const initialState = {
  videos: [],
  videoFetchStatus: "idle", // 'idle', | 'loading', | 'succeeded', | 'failed'
  videoFetchError: "",
  videoUpdateStatus: "idle",
  videoUpdateError: "",
  videoCreateStatus: "idle",
};

export const createVideo = createAsyncThunk(
  "video/createVideo",
  async (videoData) => {
    try {
      const { data } = await axios.post("videos", videoData, {
        ...axiosConfig,
      });
      return data.video;
    } catch (error) {
      return Promise.reject(error);
    }
  }
);

export const fetchVideos = createAsyncThunk(
  "video/fetchVideos",
  async (params) => {
    try {
      const { data } = await axios.get("videos", {
        ...axiosConfig,
        params, // params: {categoryId, chapterId}
      });
      return data.videos;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);

export const updateVideo = createAsyncThunk(
  "video/updateVideo",
  async ({ id, video }) => {
    try {
      const { data } = await axios.patch("videos/" + id, video, {
        ...axiosConfig,
      });
      return data.video;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);

export const deleteVideo = createAsyncThunk(
  "video/deleteVideo",
  async (videoId, { rejectWithValue }) => {
    try {
      const { data } = await axios.delete("videos/" + videoId, {
        ...axiosConfig,
      });
      return data.video;
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);

export const videoSlice = createSlice({
  name: "video",
  initialState,
  reducers: {},
  extraReducers: {
    // Fetch chapters
    [fetchVideos.pending]: (state) => {
      state.videoFetchStatus = "loading";
      state.videoFetchError = "";
    },
    [fetchVideos.fulfilled]: (state, action) => {
      state.videos = action.payload;
      state.videoFetchStatus = "secceeded";
    },
    [fetchVideos.rejected]: (state, action) => {
      state.videoFetchStatus = "failed";
      state.videos = [];
      state.videoFetchError = action.error.message;
    },

    [createVideo.fulfilled]: (state, action) => {
      state.videos = state.videos.concat(action.payload);
    },

    [deleteVideo.fulfilled]: (state, action) => {
      state.videos = state.videos.filter(
        (video) => video._id !== action.payload._id
      );
    },

    // Update video
    [updateVideo.pending]: (state, action) => {
      state.videoUpdateStatus = "loading";
    },
    [updateVideo.rejected]: (state, action) => {
      state.videoUpdateStatus = "failed";
      state.videoUpdateError = action.error.message;
    },

    [updateVideo.fulfilled]: (state, action) => {
      state.videoUpdateStatus = "succeeded";
      state.videos = state.videos.map((video) =>
        video._id === action.payload._id ? action.payload : video
      );
      state.videoUpdateError = "";
    },
  },
});

export default videoSlice.reducer;
