import React from "react";

export const TableHead = ({ columnData }) => {
  return (
    <thead className="text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400">
      <tr>
        <th scope="col" className="p-4">
          <div className="flex items-center">
            <input
              id="checkbox-all"
              type="checkbox"
              className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
            />
            <label htmlFor="checkbox-all" className="sr-only">
              checkbox
            </label>
          </div>
        </th>
        {columnData.map((column) => {
          return column.action ? (
            <th scope="col" className="px-4 py-3" key={column.field}>
              {column?.actionButton ? column.actionButton : <span className="sr-only">{column.field}</span>}
            </th>
          ) : (
            <th scope="col" className="px-2 py-3" key={column.field}>
              {column.field}
            </th>
          );
        })}
      </tr>
    </thead>
  );
};
