/**
 *
 * @param {Array[subject]} subjects
 * @returns
 */
export function getDistinctClasses(subjects) {
  const classes = [...new Set(subjects.map((subject) => subject.class))].map(
    (clas) => ({ id: clas, name: `Class ${clas}` })
  );
  return classes;
}
