import React from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { createChapters } from "../../features/chapter/chapter-slice";
import { UploadForm } from "../forms/UploadForm";

export const ChapterUploadForm = ({ categoryId }) => {
  const [radioState, setRadioState] = useState("premium-no");
  const [files, setFiles] = useState([]);
  const { chaptersCreateStatus } = useSelector((state) => state.chapter);
  const dispatch = useDispatch();
  const inputChangeHandler = (e) => {
    setRadioState(e.target.id);
  };

  const fileChangeHandler = (e) => {
    e.preventDefault();
    const files = [].slice.call(e.target.files);
    setFiles(files);
  };

  const removeFile = (name) => {
    setFiles((files) => files.filter((file) => file.name !== name));
  };

  const saveHandler = async (e) => {
    e.preventDefault();
    if (files.length === 0) return;
    const isPaid = radioState === "premium-yes";
    const formData = new FormData();
    formData.append("isPaid", isPaid);
    formData.append("categoryId", categoryId);
    for (const file of files) {
      formData.append("files", file);
    }
    await dispatch(createChapters(formData)).unwrap();
    setFiles([]);
  };

  return (
    <UploadForm
      title="Upload Chapters"
      inputChangeHandler={inputChangeHandler}
      radioState={radioState}
      fileChangeHandler={fileChangeHandler}
      files={files}
      removeFile={removeFile}
      saveHandler={saveHandler}
      status={chaptersCreateStatus}
    />
  );
};
