import { Fragment, useState } from 'react'
import { Listbox, Transition } from '@headlessui/react'
import { CheckIcon, ChevronUpDownIcon } from '@heroicons/react/20/solid'
import { useEffect } from 'react'


function classNames(...classes) {
    return classes.filter(Boolean).join(' ')
}

export default function Menu({ label, onSelect, options = [], selectedKey, wrapperClasses, buttonClasses }) {
    const [selectedId, setSelectedId] = useState(selectedKey)


    useEffect(() => {
        if (selectedId != selectedKey) setSelectedId(selectedKey)
    }, [selectedKey])
    const selectedOption = options?.length > 0 && selectedId ? options.find(option => option.id === selectedId) : options[0]

    const handleChange = (selected) => {
        setSelectedId(selected.id)
        if (onSelect) onSelect(selected["id"])
    }

    const _buttonClasses = "relative w-full cursor-default rounded-md border border-gray-300 dark:border-slate-500 bg-white dark:bg-black py-2 pl-3 pr-10 text-left shadow-sm focus:border-indigo-500 focus:outline-none focus:ring-1 focus:ring-indigo-500 sm:text-sm " + buttonClasses
    if (options.length == 0) return
    return (
        <div className={wrapperClasses}>
            <Listbox value={selectedOption} onChange={handleChange}>
                {({ open }) => (
                    <>
                        {label && <Listbox.Label className="block text-sm font-semibold py-1 text-gray-700 dark:text-gray-200 ">{label}</Listbox.Label>}
                        <div className="relative mt-1">
                            <Listbox.Button className={_buttonClasses} >
                                <span className="block truncate font-medium text-white">{selectedOption?.name}</span>
                                <span className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-2">
                                    <ChevronUpDownIcon className="h-5 w-5 text-gray-400 dark:text-gray-200" aria-hidden="true" />
                                </span>
                            </Listbox.Button>

                            <Transition
                                show={open}
                                as={Fragment}
                                leave="transition ease-in duration-100"
                                leaveFrom="opacity-100"
                                leaveTo="opacity-0"
                            >
                                <Listbox.Options className="absolute z-30 mt-1 max-h-60 w-full overflow-auto rounded-md bg-white dark:bg-black py-1 text-base shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none sm:text-sm">
                                    {options.map((option) => (
                                        <Listbox.Option
                                            key={option.id}
                                            className={({ active }) =>
                                                classNames(
                                                    active ? 'text-white bg-indigo-600' : 'text-gray-900 dark:text-gray-200',
                                                    'relative cursor-default select-none py-2 pl-3 pr-9'
                                                )
                                            }
                                            value={option}
                                        >
                                            {({ selected, active }) => (<>
                                                <span className={classNames(selected ? 'font-semibold' : 'font-normal', 'block truncate cursor-pointer')}>
                                                    {option.name}
                                                </span>
                                                {selected ? (
                                                    <span
                                                        className={classNames(
                                                            active ? 'text-white ' : 'text-indigo-600',
                                                            'absolute inset-y-0 right-0 flex items-center pr-4'
                                                        )}
                                                    >
                                                        <CheckIcon className="h-5 w-5" aria-hidden="true" />
                                                    </span>
                                                ) : null}
                                            </>)
                                            }
                                        </Listbox.Option>
                                    ))}
                                </Listbox.Options>
                            </Transition>
                        </div>
                    </>
                )}
            </Listbox>
        </div>
    )
}
