import React, { useState } from "react";
import { Modal } from "../modal/Modal";
import { useDispatch } from "react-redux";
import { updateChapter } from "../../features/chapter/chapter-slice";
import { Link } from "react-router-dom";
import { ChapterUpdateForm } from "./ChapterUpdateForm";
import { toast } from "react-toastify"
import { timeSince } from "../../utils/datetime";
export const ChapterRow = ({ chapter, handleDeleteClick, breadcrumbsData }) => {
  const { _id, name, isPaid, type, hasExercise, updatedAt, link } = chapter;
  const [showModal, setShowModal] = useState(false);
  const dispatch = useDispatch();
  const lastUpdateTime = timeSince(updatedAt)


  const editClickHandler = async (chapter) => {

    try {
      await dispatch(updateChapter({ id: _id, chapter })).unwrap();
      setShowModal(false)
      toast.success(`${name} updated successfully`)
    } catch (error) {
      toast.dark(error?.error)
    }
  };

  return (
    <>
      <tr
        key={_id}
        className="bg-white border-b dark:bg-gray-800 dark:border-gray-700"
      >
        <td className="w-4 p-4">
          <div className="flex items-center">
            <input
              id="checkbox-table-1"
              type="checkbox"
              className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
            />
            <label htmlFor="checkbox-table-1" className="sr-only">
              checkbox
            </label>
          </div>
        </td>
        <td
          className="px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap"
        >
          <div className="flex flex-col gap-2 " >
            <span>{name}</span>
            <span className="text-xs font-semibold text-gray-600">{`${lastUpdateTime ? `This file was edited ${lastUpdateTime} ` : ""}`}</span>
          </div>
        </td>
        {
          !hasExercise && <td className="px-6 py-4 text-right">
            <button
              className="font-medium text-white bg-green-500 dark:bg-green-600 px-4 py-2 rounded-md shadow-sm hover:underline"
              onClick={() => setShowModal(true)}
            >
              Edit
            </button>
          </td>
        }

        <td className="px-6 py-4 text-right">
          {hasExercise ?
            <Link
              className="font-medium text-white bg-blue-500 dark:bg-blue-600 px-4 py-2 rounded-md shadow-sm hover:underline"
              to={`${chapter._id}?lang=${breadcrumbsData["lang"]}&subjectId=${breadcrumbsData?.subject?._id}`}
              state={{
                name,
                type,
                ...breadcrumbsData,
              }}
            >
              View
            </Link> : <a href={link}
              download
              target="_blank"
              rel="noreferrer"
              className="font-medium text-white bg-blue-500 dark:bg-blue-600 px-4 py-2 rounded-md shadow-sm hover:underline"
            > Download</a>
          }
        </td>
        {hasExercise && <td className="px-6 py-4 text-right">
          <button
            className="font-medium text-white bg-green-500 dark:bg-green-600 px-4 py-2 rounded-md shadow-sm hover:underline"
            onClick={() => setShowModal(true)}
          >
            Edit
          </button>
        </td>}


        <td className="px-6 py-4 text-right">
          <button
            className="font-medium text-white bg-red-500 dark:bg-red-600 px-4 py-2 rounded-md shadow-sm hover:underline"
            onClick={() => handleDeleteClick(_id)}
          >
            Delete
          </button>
        </td>
        <td
          className={`px-6 py-4  text-center ${isPaid ? "text-slate-600" : "text-slate-300"
            }`}
        >
          {isPaid ? "Paid" : "Free"}
        </td>
      </tr>
      {showModal && (
        <Modal show={showModal} toggle={setShowModal} title="Edit chapter">
          <ChapterUpdateForm
            chapter={chapter}
            handleSubmit={editClickHandler}
          />
        </Modal>
      )}
    </>
  );
};
