import React, { useState } from "react";
import { Modal } from "../modal/Modal";
import { CategoryForm } from "./CategoryForm";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { updateCategory } from "../../features/category/category-slice";
export const CategoryRow = ({ category, handleDeleteClick, subjectId }) => {
  const [showModal, setShowModal] = useState(false);
  const dispatch = useDispatch();
  const { _id, name, isPaid, status, hasExercise, subject, type, order, language, tag } = category;

  const editClickHandler = (categoryData) => {
    dispatch(updateCategory({ ...category, ...categoryData }));
    setShowModal(false);
  };
  return (
    <>
      <tr
        key={_id}
        className="bg-white border-b dark:bg-gray-800 dark:border-gray-700"
      >
        <td className="w-4 p-4">
          <div className="flex items-center">
            <input
              id="checkbox-table-1"
              type="checkbox"
              className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
            />
            <label htmlFor="checkbox-table-1" className="sr-only">
              checkbox
            </label>
          </div>
        </td>
        <th
          scope="row"
          className="px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap"
        >
          {name}
          {tag && <span className=" bg-blue-400 py-0.5 px-2 mx-2 rounded-sm ">{tag}</span>}
        </th>
        <td
          className={`px-6 py-4 first-letter:uppercase ${status !== "active" ? "text-slate-600" : "text-slate-300"}`}
        >
          {status}
        </td>
        <td
          scope="row"
          className="px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap"
        >
          {order}
        </td>
        <td className="px-6 py-4 text-right">
          <Link
            className="font-medium text-white hover:underline bg-blue-500 dark:bg-blue-600 px-4 py-2 rounded-md shadow-sm"
            to={`${category._id}?lang=${language}&subjectId=${subjectId}`}
            state={{ hasExercise, subject, type, name, clas: category.class, lang: language }}
          >
            View
          </Link>
        </td>
        <td className="px-6 py-4 text-right">
          <button
            className="font-medium text-white bg-green-500 dark:bg-green-600 px-4 py-2 rounded-md shadow-sm hover:underline"
            onClick={() => setShowModal(true)}
          >
            Edit
          </button>
        </td>

        <td className="px-6 py-4 text-right">
          <button
            className="font-medium text-white bg-red-500 dark:bg-red-600 px-4 py-2 rounded-md shadow-sm hover:underline"
            onClick={() => handleDeleteClick(_id)}
          >
            Delete
          </button>
        </td>
        <td
          className={`px-6 py-4 ${isPaid ? "text-slate-600" : "text-slate-300"
            }`}
        >
          {isPaid ? "Paid" : "Free"}
        </td>
      </tr>
      {showModal && (
        <Modal show={showModal} toggle={setShowModal} title="Edit Category">
          <CategoryForm category={category} handleSubmit={editClickHandler} />
        </Modal>
      )}
    </>
  );
};
