import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { axiosConfig } from "../../config/axios-config";

const initialState = {
    notifications: [],
    notificationFetchStatus: "idle", // 'idle', | 'loading', | 'succeeded', | 'failed'
    notificationFetchError: "",
    notificationCreateStatus: "idle",
    notificationDeleteError: "",
    notificationCreateError: ""
};

export const createNotification = createAsyncThunk(
    "notification/createNotification",
    async (notificationData, { rejectWithValue }) => {
        try {
            const { data } = await axios.post("notifications", notificationData, {
                ...axiosConfig,
            });
            return data.notification;
        } catch (error) {
            return rejectWithValue(error?.response?.data);
        }
    }
);


export const fetchNotifications = createAsyncThunk(
    "notification/fetchNotifications",
    async (params) => {
        try {
            const { data } = await axios.get("notifications", {
                ...axiosConfig,
                params, // params: {days:30}
            });
            return data.notifications;
        } catch (error) {
            return Promise.reject(error.message);
        }
    }
);

export const deleteNotification = createAsyncThunk(
    "notification/deleteNotification",
    async (notificationId, { rejectWithValue }) => {
        try {
            const { data } = await axios.delete("notification/" + notificationId, {
                ...axiosConfig,
            });
            return data.notification;
        } catch (error) {
            return rejectWithValue(error?.response?.data);
        }
    }
);

export const notificationSlice = createSlice({
    name: "notification",
    initialState,
    reducers: {},
    extraReducers: {
        // Fetch chapters
        [fetchNotifications.pending]: (state) => {
            state.notificationFetchStatus = "loading";
            state.notificationFetchError = "";
        },
        [fetchNotifications.fulfilled]: (state, action) => {
            state.notifications = action.payload;
            state.notificationFetchStatus = "succeeded";
        },
        [fetchNotifications.rejected]: (state, action) => {
            state.notificationFetchStatus = "failed";
            state.chapters = [];
            state.notificationFetchError = action.error.message;
        },

        [createNotification.pending]: (state, action) => {
            state.notificationCreateStatus = "loading"
        },
        [createNotification.rejected]: (state, action) => {
            state.notificationCreateStatus = "failed"
            state.notificationCreateError = action.error.message
        },
        [createNotification.fulfilled]: (state, action) => {
            state.notifications.push(action.payload);
            state.notificationCreateStatus = "succeeded"
        },



        [deleteNotification.fulfilled]: (state, action) => {
            state.notifications = state.notifications.filter(
                (notification) => notification._id !== action.payload._id
            );

        },

        [deleteNotification.rejected]: (state, action) => {
            state.notificationDeleteError = action.error
        },
    },
});
export const selectNotification = (state) => state.notification.notifications;

export default notificationSlice.reducer;
