import React from "react";
import { ButtonLoader } from "../button/ButtonLoader";
import { RadioInput } from "../inputs/RadioInput";

export const UploadForm = ({
  inputChangeHandler,
  radioState,
  fileChangeHandler,
  files,
  removeFile,
  saveHandler,
  status,
  title,
}) => {
  return (
    <form className="max-w-lg shrink-0 p-2">
      <h2 className="text-xl my-2">{title}</h2>
      <div className="flex gap-2 items-center">
        <span className="block text-sm font-medium text-gray-900 dark:text-gray-400">
          Premium ?
        </span>
        <RadioInput
          id="premium-yes"
          name="isPaid"
          value={true}
          label="Yes"
          checked={radioState === "premium-yes"}
          handleChange={inputChangeHandler}
        />
        <RadioInput
          id="premium-no"
          name="isPaid"
          value={false}
          label="No"
          checked={radioState === "premium-no"}
          handleChange={inputChangeHandler}
        />
      </div>
      <div className="mt-1">
        <label
          className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300"
          htmlFor="chapters"
        >
          Upload file
        </label>
        <input
          className="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50 dark:text-gray-400 focus:outline-none dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400"
          aria-describedby="chapters"
          id="chapters"
          type="file"
          multiple
          onChange={fileChangeHandler}
        />
      </div>
      {files.length !== 0 && (
        <ul className="text-sm max-h-[60vh] overflow-y-auto my-2 font-medium text-gray-900 bg-white border border-gray-200 rounded-lg dark:bg-gray-700 dark:border-gray-600 dark:text-white">
          {files.map(({ name }, index) => {
            return (
              <li
                key={index}
                className="flex items-center justify-between gap-2 p-2 border-b border-gray-200 rounded-t-lg dark:border-gray-600 "
              >
                <span className="w-60 truncate">{name}</span>

                <button type="button" onClick={() => removeFile(name)}>
                  <svg
                    className="w-5 h-5"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                      clipRule="evenodd"
                    ></path>
                  </svg>
                </button>
              </li>
            );
          })}
        </ul>
      )}

      <ButtonLoader
        loading={status === "loading"}
        disabled={status === "loading" || files.length === 0}
        text="Save"
        clickHandler={saveHandler}
      />
    </form>
  );
};
