import axios from 'axios';
import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import { ButtonLoader } from '../../components/button/ButtonLoader';
import ComboboxStyled from '../../components/inputs/Combobox';
import { Input } from '../../components/inputs/Input';
import Menu from '../../components/Menu';
import { Modal } from '../../components/modal/Modal';
import { createSubject, updateSubject } from '../../features/subject/subject-slice';

const options = [
    { id: "1", name: "Class 1" },
    { id: "2", name: "Class 2" },
    { id: "3", name: "Class 3" },
    { id: "4", name: "Class 4" },
    { id: "5", name: "Class 5" },
    { id: "6", name: "Class 6" },
    { id: "7", name: "Class 7" },
    { id: "8", name: "Class 8" },
    { id: "9", name: "Class 9" },
    { id: "10", name: "Class 10" },
    { id: "11", name: "Class 11" },
    { id: "12", name: "Class 12" },
]

const SubjectForm = ({ subject, setShowModal, showModal, handleClose }) => {
    const dispatch = useDispatch()
    const [details, setDetails] = useState({})
    const [editingSubjectId, setEditingSubjectId] = useState()
    const { name = "", clas = "", order = 0, image = null, active = true, localizedName = "", parent } = details;


    const { subjectUpdateStatus, subjectCreateStatus, subjects } = useSelector(state => state.subject)
    const isLoading = subjectUpdateStatus === "loading" || subjectCreateStatus === "loading"

    const [imageUrl, setImageURL] = useState()

    const isFormCompleted = !!name && !!order && editingSubjectId ? !!imageUrl : true
    const parentSubjects = subjects?.reduce((acc, subject) => {
        if (subject.class === clas && subject._id !== editingSubjectId) {
            return [...acc, { name: subject.name, id: subject._id }]
        }
        return acc
    }, [])

    const inputChangeHandler = (e) => {
        if (e.target.type === "checkbox") {
            setDetails({ ...details, [e.target.name]: e.target.checked });
        } else
            setDetails({ ...details, [e.target.name]: e.target.value });
    };

    const fileChangeHandler = (e) => {
        if (e.target.files && e.target.files.length > 0) {
            const file = e.target.files[0]
            setDetails({ ...details, [e.target.name]: file })
            getSRCFromImageFIle(file)
        }
    }

    const saveHandler = async (e) => {
        e.preventDefault();
        if (!name) return;

        const formDetails = { name, localizedName, clas, image, order, active, parent }
        const formData = new FormData()

        for (const key in formDetails) {
            if (Object.hasOwnProperty.call(formDetails, key)) {
                const value = formDetails[key];
                if (value != undefined) {
                    formData.append(key, value)
                }
            }
        }
        try {
            if (!editingSubjectId) await dispatch(createSubject(formData)).unwrap();
            else await dispatch(updateSubject({ subject: formData, _id: editingSubjectId })).unwrap()
            setDetails({})
            setImageURL(null)
            toast.success(editingSubjectId ? "Subject updated successfully" : "Subject created successfully")
            setShowModal(false)
            if (handleClose) handleClose()
        } catch (error) {
            toast.dark(error?.message)
        }
    };

    const getSRCFromImageFIle = (file) => {
        let reader = new FileReader();
        reader.onloadend = function () {
            setImageURL(reader.result)
        }
        reader.readAsDataURL(file);
    }

    const handleRemoveImage = () => {
        setImageURL(null);
        setDetails(details => ({ ...details, image: null }));
    }

    const handleStatusSelection = (key) => {
        const isActive = key === "active" ? true : false;
        setDetails(details => ({ ...details, active: isActive }))
    }

    useEffect(() => {
        setDetails({ name: subject?.name, clas: subject?.class, order: subject?.order, active: subject?.active, localizedName: subject?.localizedName, parent: subject?.parent });
        setImageURL(subject?.imageUrl)
        setEditingSubjectId(subject?._id)
    }, [subject])

    return <Modal
        show={showModal}
        title="Add or update Subject"
        resetWidth={false}
        maxWidth="w-full max-w-xl"
        toggle={setShowModal}
        onClose={handleClose}
    >
        <form className="w-full shrink-0">
            <Input
                id="name"
                value={name}
                label="Name"
                placeholder="Name"
                handleChange={inputChangeHandler}
                name="name"
            />
            <Input
                id="localizedName"
                value={localizedName}
                label="Hindi Name"
                placeholder="Hindi name"
                handleChange={inputChangeHandler}
                name="localizedName"
            />
            <Menu label="Status"
                options={[{ id: "active", name: "Active" }, { id: "inactive", name: "Inactive" }]}
                onSelect={handleStatusSelection}
                selectedKey={active ? "active" : "inactive"}
                buttonClasses="dark:bg-gray-700"
                wrapperClasses="mb-2"
            />

            <ComboboxStyled
                onSelect={(key) => inputChangeHandler({ target: { name: "clas", value: key } })}
                options={options}
                selectedKey={clas}
                displayKey="id"
                wrapperClasses="mb-2"
                label="Class" />

            <Input
                id="order"
                value={order}
                label="Order"
                placeholder="order"
                handleChange={inputChangeHandler}
                name="order"
                type='number'
                min={0}
            />

            <Menu label="Parent Subject"
                options={[{ name: "---------", id: "" }, ...parentSubjects]}
                onSelect={(subject) => inputChangeHandler({ target: { value: subject, name: "parent" } })}
                selectedKey={parent}
                wrapperClasses="mb-2"
                buttonClasses="dark:bg-gray-700" />

            {
                !!imageUrl == true ?
                    (<div className='relative border border-gray-600 p-1 rounded-md flex items-center justify-center max-h-52'>
                        <button className='p-0 text-gray-400  absolute top-1 right-1' type='button' onClick={handleRemoveImage}>
                            <svg
                                className="w-5 h-5"
                                fill="currentColor"
                                viewBox="0 0 20 20"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <path
                                    fillRule="evenodd"
                                    d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                                    clipRule="evenodd"
                                ></path>
                            </svg>
                        </button>
                        <img src={imageUrl} alt='subject cover photo' className='max-h-32 w-auto object-cover' />
                    </div>) :
                    (<div className='my-2'>
                        <label
                            className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300"
                            htmlFor="icon"
                        >
                            Upload Image
                        </label>
                        <input
                            className="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50 dark:text-gray-400 focus:outline-none dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400"
                            aria-describedby="icon"
                            id="image"
                            type="file"
                            multiple={false}
                            name="image"
                            accept=".jpeg,.jpg,.png,"
                            onChange={fileChangeHandler}
                        />
                    </div>)
            }
            <div className='py-2'>
                <ButtonLoader clickHandler={saveHandler}
                    text="Save"
                    loading={isLoading}
                    disabled={!isFormCompleted}
                />
            </div>
        </form>
    </Modal>

}

export default SubjectForm