import React from "react";

export const BreadCrumb = ({ children }) => {
  return (
    <nav
      className="flex flex-1 sticky left-0 top-0 w-full py-3 px-5 text-gray-700 bg-gray-50 rounded-lg border border-gray-200 dark:bg-gray-800 dark:border-gray-700"
      aria-label="Breadcrumb"
    >
      <ol className="inline-flex items-center space-x-1 md:space-x-3">
        {children}
      </ol>
    </nav>
  );
};
