import axios from 'axios';
import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import Search from '../../components/inputs/Search'
import Menu from '../../components/Menu'
import { Popup } from '../../components/modal/Popup';
import TableSkeleton from '../../components/skeleton/TableSkeleton';
import { TableHead } from '../../components/table/TableHead';
import { deleteSubject } from '../../features/subject/subject-slice';
import { getDistinctClasses } from '../../utils';
import SubjectForm from './SubjectForm';
import SubjectRow from './SubjectRow';
const columnData = [
    { field: "name", action: false },
    { field: "class", action: false },
    { field: "order", action: false },
    { field: "status", action: false },
    { field: "edit", action: true },
    { field: "delete", action: true },
    { field: "icon", action: false }
];

// const fetchSubjects = async () => {
//     const { data } = await axios.get(`subjects?clas=all`, axiosConfig)
//     return data
// }

function searchSubjects(list, query) {
    if (!query) return list
    const regex = new RegExp(query, "ig");
    return list.filter(
        subject => subject.name.match(regex) || subject.class === query
    );
}

function getClasses(list) {
    const classes = getDistinctClasses(list);
    return [{ id: "all", name: "All", }, ...classes]
}

const Subject = () => {
    const dispatch = useDispatch();
    const [subject, setSubject] = useState()
    const [showPopup, setShowPopup] = useState(false)
    const [menuOptions, setMenuOptions] = useState([])
    const [selectedClass, setSelectedClass] = useState("")
    const [text, setText] = useState("")
    const [showModal, setShowModal] = useState(false)

    const { subjects, subjectFetchStatus } = useSelector(state => state.subject)
    const isLoading = subjectFetchStatus === "loading"

    // useEffect(() => {
    //     dispatch(fetchAllSubjects({ clas: "all" }))
    // }, [])

    useEffect(() => {
        if (subjects && subjects.length > 0) {
            setMenuOptions(getClasses(subjects))
        }
    }, [subjects])


    const handleDeletePopup = (id) => {
        setSubject({ id });
        setShowPopup(true);
    };

    const handleDelete = async () => {
        try {
            await dispatch(deleteSubject(subject.id)).unwrap();
            toast.success("Successfully deleted subject")
            setShowPopup(false);
            setSubject(null)
        } catch (error) {
            toast.error(error?.message)
        }
    };

    const handleEditClick = (subject) => {
        setSubject(subject)
        setShowModal(true)
    }

    const filteredSubjects = searchSubjects(subjects, text).filter(subject => ["all", ""].includes(selectedClass) ? true : subject.class === selectedClass)

    return (
        <div className='px-2 h-full overflow-auto'>
            <div className="flex items-center justify-between sticky top-0 z-20 bg-black py-2 px-4" >
                <Menu options={menuOptions} onSelect={(key) => setSelectedClass(key)} wrapperClasses="w-40" />
                <Search handleSearch={(text) => setText(text)} placeholder="Search for a subject" />
            </div>
            {
                isLoading ?
                    (<TableSkeleton />) :
                    (<table className=" text-sm text-left text-gray-500 dark:text-gray-400 w-full">
                        <TableHead columnData={columnData} />
                        <tbody>
                            {
                                filteredSubjects?.map(subject =>
                                    <SubjectRow
                                        key={subject._id}
                                        subject={subject}
                                        handleDeleteClick={handleDeletePopup}
                                        handleEditActionClick={handleEditClick}
                                    />)
                            }
                        </tbody>
                    </table>)
            }

            {/* </div> */}
            <button
                type="button"
                onClick={() => {
                    setSubject({});
                    setShowModal(true);
                }}
                className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-full text-sm p-2.5 text-center inline-flex items-center mr-2 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800 absolute bottom-2 right-2"
            >
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6"
                >
                    <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M12 4.5v15m7.5-7.5h-15"
                    />
                </svg>
                <span className="sr-only">Icon description</span>
            </button>
            <SubjectForm showModal={showModal} setShowModal={setShowModal} subject={subject} handleClose={() => setSubject({})} />
            <Popup setShow={setShowPopup} show={showPopup} actionOk={handleDelete} title=" Are you sure you want to delete this subject" />
        </div>
    )
}



export default Subject