import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { axiosConfig } from "../../config/axios-config";

const initialState = {
  exercises: [],
  exerciseFetchStatus: "idle", // 'idle', | 'loading', | 'succeeded', | 'failed'
  exerciseFetchError: "",
  exerciseUpdateStatus: "idle",
  exerciseUpdateError: "",
  exerciseCreateStatus: "idle",
  exerciseCreateError: "idle",
};

export const createExercises = createAsyncThunk(
  "exercise/createExercises",
  async (exerciseData,{rejectWithValue}) => {
    try {
      const { data } = await axios.post("exercises", exerciseData, {
        ...axiosConfig,
        "Content-Type": "multipart/form-data",
      });
      return data.exercises;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const fetchExercises = createAsyncThunk(
  "exercise/fetchExercises",
  async (params) => {
    try {
      const { data } = await axios.get("exercises", {
        ...axiosConfig,
        params, // params: {categoryId, chapterId}
      });
      return data.exercises;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);

export const updateExercise = createAsyncThunk(
  "exercise/updateExercise",
  async ({ id, exercise },{rejectWithValue}) => {
    try {
      const { data } = await axios.patch("exercise/" + id, exercise, {
        ...axiosConfig,
      });
      return data.exercise;
    } catch (error) {
      return rejectWithValue(error?.response?.data);
    }
  }
);

export const deleteExercise = createAsyncThunk(
  "exercise/deleteExercise",
  async (exerciseId) => {
    try {
      const { data } = await axios.delete("exercise/" + exerciseId, {
        ...axiosConfig,
      });
      return data.exercise;
    } catch (error) {
      return Promise.reject(error.message);
    }
  }
);

export const exerciseSlice = createSlice({
  name: "exercise",
  initialState,
  reducers: {},
  extraReducers: {
    // Fetch chapters
    [fetchExercises.pending]: (state) => {
      state.exercises = [];
      state.exerciseFetchStatus = "loading";
      state.exerciseFetchError = "";
    },
    [fetchExercises.fulfilled]: (state, action) => {
      state.exercises = action.payload;
      state.exerciseFetchStatus = "secceeded";
    },
    [fetchExercises.rejected]: (state, action) => {
      state.exerciseFetchStatus = "failed";
      state.exercises = [];
      state.exerciseFetchError = action.error.message;
    },

    [createExercises.fulfilled]: (state, action) => {
      state.exercises = state.exercises.concat(action.payload);
      state.exerciseCreateStatus = "succeeded";
      state.exerciseCreateError = ""
    },
    [createExercises.pending]: (state, action) => {
      state.exerciseCreateStatus = "loading";
    },
    [createExercises.rejected]: (state, action) => {
      state.exerciseCreateStatus = "failed";
      state.exerciseCreateError = action.payload.message
    },

    [deleteExercise.fulfilled]: (state, action) => {
      state.exercises = state.exercises.filter(
        (exercise) => exercise._id !== action.payload._id
      );
    },

    // Update exercise
    [updateExercise.pending]: (state, action) => {
      state.exerciseUpdateStatus = "loading";
    },
    [updateExercise.rejected]: (state, action) => {
      state.exerciseUpdateStatus = "failed";
      state.exerciseUpdateError = action.payload.message;
    },

    [updateExercise.fulfilled]: (state, action) => {
      state.exerciseUpdateStatus = "succeeded";
      state.exercises = state.exercises.map((exercise) =>
        exercise._id === action.payload._id ? action.payload : exercise
      );
      state.exerciseUpdateError = "";
    },
  },
});

export default exerciseSlice.reducer;
