import React, { useRef } from 'react'
import { useState } from 'react'
import { useOnClickOutside } from '../hooks/useOnClickOutside'

const Dropdown = ({ options, onOptionClick,position }) => {
    const [isOpen, setIsOpen] = useState(false)
    const ref = useRef();
    if (!options || options.length === 0) return

    useOnClickOutside(ref, () => setIsOpen(false));
    const positionClasses = position == "right" ? "right-0 top-6" : position == "left" ? "left-2 top-6": "top-6"
    return (
        <div ref={ref} className="relative">
            <button  onClick={() => setIsOpen(v=>!v)} className="inline-block text-gray-500 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-700 focus:ring-4 focus:outline-none focus:ring-gray-200 dark:focus:ring-gray-700 rounded-lg text-sm p-1.5" type="button">
                <span className="sr-only">Open dropdown</span>
                <svg className="w-6 h-6" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path d="M6 10a2 2 0 11-4 0 2 2 0 014 0zM12 10a2 2 0 11-4 0 2 2 0 014 0zM16 12a2 2 0 100-4 2 2 0 000 4z"></path></svg>
            </button>

            {
                isOpen && <div id="dropdown" className={"z-50 text-base list-none bg-white divide-y divide-gray-100 rounded shadow w-36 dark:bg-gray-900 absolute " + positionClasses }>
                    <ul className="py-1" aria-labelledby="dropdownButton">
                        {
                            options.map(option => {
                                return <li key={option.key}>
                                    <button onClick={() => onOptionClick(option)} className="w-full block px-4 py-2 text-sm text-left text-gray-700 hover:bg-gray-100 dark:hover:bg-gray-600 dark:text-gray-200 dark:hover:text-white">{option.display}</button>
                                </li>
                            })
                        }

                    </ul>
                </div>
            }
        </div>
    )
}

export default Dropdown