import React from "react";
import { useState } from "react";
import { Input } from "../inputs/Input";
import { RadioInput } from "../inputs/RadioInput";
import { useSelector } from "react-redux/es/exports";
import { ButtonLoader } from "../button/ButtonLoader";

export const CategoryForm = ({ category = {}, handleSubmit }) => {
  const [categoryState, setCategoryState] = useState({
    name: category.name || "",
    hasExercise: category.hasExercise || false,
    isPaid: category.isPaid || false,
    status: category.status || "active",
    order: category.order || 0,
    type: category.type || "pdf",
    language: category.language,
    tag: category.tag || ""
  });
  const { name, hasExercise, isPaid, status, order, type, language, tag } = categoryState;
  const [radioState, setRadioState] = useState({
    isPaid: isPaid ? "premium-yes" : "premium-no",
    hasExercise: hasExercise ? "yes" : "no",
    language
  });
  const { categoryStatus } = useSelector((state) => state.category);
  const inputChangeHandler = (e) => {
    const { type, name, value, id } = e.target
    if (type === "radio") {
      setRadioState({ ...radioState, [name]: id });
      let _value = name === "language" ? value : value === "true" ? true : false
      setCategoryState({
        ...categoryState,
        [name]: _value,
      });
    } else
      setCategoryState({ ...categoryState, [e.target.name]: e.target.value });
  };

  const saveHandler = (e) => {
    e.preventDefault();
    if (name) {
      handleSubmit(categoryState);
      setCategoryState({
        name: category.name || "",
        hasExercise: category.hasExercise || false,
        isPaid: category.isPaid || false,
        status: category.status || "active",
        order: category.order || 0,
        type: category.type || "pdf",
        language: category.language,
        tags: category.tag || ""
      });
    }
  };
  return (
    <form>
      <Input
        id="name"
        value={name}
        label="Category Name"
        placeholder="Name"
        handleChange={inputChangeHandler}
        name="name"
      />
      <Input
        id="order"
        value={order}
        label="Order"
        placeholder="Order"
        handleChange={inputChangeHandler}
        name="order"
        min="0"
        type="number"
      />
      <Input
        id="tag"
        value={tag}
        label="Tag"
        placeholder="Add tag"
        handleChange={inputChangeHandler}
        name="tag"
      />

      <div className="mb-2">
        <label
          htmlFor="status"
          className="block text-sm font-medium text-gray-900 dark:text-gray-400"
        >
          Status
        </label>
        <select
          id="status"
          value={status}
          name="status"
          onChange={inputChangeHandler}
          className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
        >
          <option value="active">Active</option>
          <option value="inactive">Inactive</option>
        </select>
      </div>
      <div className="mb-2">
        <label
          htmlFor="status"
          className="block  text-sm font-medium text-gray-900 dark:text-gray-400"
        >
          Content Type
        </label>
        <select
          id="type"
          value={type}
          name="type"
          onChange={inputChangeHandler}
          className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
        >
          <option value="pdf">PDF</option>
          <option value="video">Video</option>
        </select>
      </div>

      <div className="flex gap-2 mb-2">
        <span className="block  text-sm font-medium text-gray-900 dark:text-gray-400">
          Allow adding exercise?
        </span>
        <RadioInput
          id="yes"
          name="hasExercise"
          label="Yes"
          value={true}
          handleChange={inputChangeHandler}
          checked={radioState.hasExercise === "yes"}
        />
        <RadioInput
          id="no"
          name="hasExercise"
          label="No"
          value={false}
          checked={radioState.hasExercise === "no"}
          handleChange={inputChangeHandler}
        />
      </div>
      <div className="flex gap-2 mb-2">
        <span className="block  text-sm font-medium text-gray-900 dark:text-gray-400">
          Premium ?
        </span>
        <RadioInput
          id="premium-yes"
          name="isPaid"
          value={true}
          label="Yes"
          checked={radioState.isPaid === "premium-yes"}
          handleChange={inputChangeHandler}
        />
        <RadioInput
          id="premium-no"
          name="isPaid"
          value={false}
          label="No"
          checked={radioState.isPaid === "premium-no"}
          handleChange={inputChangeHandler}
        />
      </div>
      {!!category.language && <div className="flex gap-2 mb-2">
        <span className="block text-sm font-medium text-gray-900 dark:text-gray-400">
          Medium
        </span>
        <RadioInput
          id="english"
          name="language"
          value="english"
          label="English"
          checked={radioState.language === "english"}
          handleChange={inputChangeHandler}
        />
        <RadioInput
          id="hindi"
          name="language"
          value="hindi"
          label="Hindi"
          checked={radioState.language === "hindi"}
          handleChange={inputChangeHandler}
        />
        <RadioInput
          id="common"
          name="language"
          value="common"
          label="Common"
          checked={radioState.language === "common"}
          handleChange={inputChangeHandler}
        />
      </div>}
      <ButtonLoader
        clickHandler={saveHandler}
        text="Save"
        loading={categoryStatus === "loading"}
        disabled={categoryStatus === "loading" || name === ""}
      />
    </form>
  );
};
